<template>
  <div class="see-details">
    <el-row class="user-info">
      <el-col :span="4" style="max-width:80px;">
        <el-avatar :src="babyInfo.user && babyInfo.user.avatar" />
      </el-col>
      <el-col :span="20">
        <span class="item-title">用户名称：</span>
        <span class="item-text"
          >{{ babyInfo.user && babyInfo.user.nickname }} &nbsp;<svg-icon
            v-if="replace"
            icon-class="replace"
          />
        </span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span class="item-title">宝宝：</span
        ><span class="item-text">{{ babyInfo.name }}</span>
        <span class="item-title" style="margin-left:10px;">宝宝月龄：</span
        ><span class="item-text">{{ babyInfo.birthday }}</span>
      </el-col>
    </el-row>
    <el-tabs
      v-model="activeName"
      style="margin-top:30px;"
      class="professional-role__header__tabs"
      @tab-click="getActiveList"
    >
      <el-tab-pane
        v-for="{ label, name, num } in tabArr"
        :key="name"
        :name="name"
      >
        <span slot="label" class="professional-role__header__tabs__slot-label"
          ><el-badge v-if="num > 0" :value="num" class="item" />{{
            label
          }}</span
        >
      </el-tab-pane>
    </el-tabs>
    <el-table
      :data="tableData"
      empty-text="暂无数据"
      style="width:100%;min-width:400px;"
    >
      <el-table-column
        :width="pageType === 'chat' ? '60px' : '110px'"
        prop="monthAge"
        label="测试月龄"
        align="center"
      />
      <el-table-column
        prop="name"
        label="发育量表名称"
        align="center"
        :width="pageType === 'chat' ? '110px' : '250px'"
      />
      <el-table-column
        prop="status"
        label="当前状态"
        align="center"
        :width="pageType === 'chat' ? '100px' : '150px'"
      />
      <el-table-column
        v-if="pageType === 'health'"
        label="最近操作时间"
        width="220px"
        align="center"
      >
        <template slot-scope="scope">
          {{ new Date(scope.row.operateTime) | formatTime() }}
        </template>
      </el-table-column>
      <el-table-column
        v-if="pageType === 'health'"
        prop="name"
        align="center"
        label="医生建议"
      >
        <template slot-scope="scope">
          {{ scope.row.suggestion ? scope.row.suggestion : '暂无数据' }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        :width="pageType === 'chat' ? '100px' : '200px'"
        align="center"
      >
        <template v-if="scope.row" slot-scope="scope">
          <el-badge
            :is-dot="
              (scope.row.status &&
                scope.row.status === '待发送' &&
                $store.state.user.bus_role === 'hm') ||
                (scope.row.status && scope.row.status === '待反馈') ||
                (scope.row.status &&
                  scope.row.status === '反馈已保存' &&
                  $store.state.user.bus_role === 'doctor')
            "
            class="dot-badge"
          >
            <el-button
              v-if="
                (scope.row.status && scope.row.status !== '待测试') ||
                  scope.row.status !== '已完成'
              "
              type="text"
              size="small"
              :disabled="!isMyUser"
              @click="sendClick(getOperation(scope.row.status), scope.row)"
              >{{ getOperation(scope.row.status) }}</el-button
            >
          </el-badge>
          <el-button type="text" size="small" @click="seeDetail(scope.row)"
            >查看</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="发送发育量表提醒"
      :visible.sync="growDialogVisible"
      width="400px"
      center
      top="30vh"
      :modal="false"
    >
      <span style="line-height: 24px;"
        >操作成功后，您将发送【{{ rowInfo.name }}】给【{{
          babyInfo.name
        }}】，如果确认无误，请单击确认发送</span
      >
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          style="padding: 6px 15px;height: 32px;"
          @click="confirmSend"
          >确认发送</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getGrowthDetail, sendGrowth } from '@/api/grow-table'
import { mapGetters } from 'vuex'
export default {
  props: {
    pageType: {
      type: String,
      default: 'health'
    },
    member: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      replace: false,
      activeName: 'all',
      tableData: [],
      allDataArr: [],
      testedArr: [],
      suggestedArr: [],
      babyInfo: {},
      statusText: '',
      rowInfo: {},
      tabArr: [
        {
          label: '全部',
          name: 'all',
          num: 0
        },
        {
          label: '医生已反馈',
          name: 'suggested',
          num: 0
        },
        {
          label: '已测',
          name: 'tested',
          num: 0
        }
      ],
      growDialogVisible: false
    }
  },
  computed: {
    ...mapGetters(['isMyUser']),
    getStatus() {
      return function(str) {
        if (str === 'un_send') return '未发送'
        if (str === 'pre_send') return '待发送'
        if (str === 'send') return '待测试'
        if (str === 'tested') return '待反馈'
        if (str === 'save') return '反馈已保存'
        if (str === 'suggested') return '已完成'
      }
    },
    getOperation() {
      return function(str) {
        if (str === '未发送' || str === '待发送') return '发送'
        if (str === '待反馈') return '填写反馈'
        if (str === '反馈已保存') return '编辑反馈'
      }
    },
    isHasUser() {
      return function(obj) {
        return (
          obj[this.$store.state.user.bus_role] === this.$store.state.user.id
        )
      }
    }
  },
  created() {
    this.getGrowthDetail()
    if (this.$route.params.id) {
      window.localStorage.setItem('paramsID', this.$route.params.id)
    }
  },
  watch: {
    member() {
      this.getGrowthDetail()
    }
  },
  methods: {
    getActiveList() {
      const name = this.activeName
      this.tableData =
        name === 'all'
          ? this.allDataArr
          : name === 'suggested'
          ? this.suggestedArr
          : this.testedArr
    },
    seeDetail(row) {
      this.statusText = row.status
      if (this.$route.name === 'growListDetail') {
        this.statusText = row.status
        this.$router.push({
          name: 'growStatusDetail',
          params: { id: row.id },
          query: { status: this.statusText }
        })
      } else {
        this.$store.state.chat.growthId = row.id
        this.$emit('setLookStatus', row.id)
      }
    },
    async getGrowthDetail() {
      const { data, code } = await getGrowthDetail({
        member: this.$route.params.id || this.member,
        needSort: this.pageType === 'health'
      })
      if (code === 20000) {
        // 发育量表的代班情况
        this.replace = data.baby.is_replace
        this.babyInfo = data.baby
        this.suggestedArr = []
        this.testedArr = []
        this.allDataArr = []
        if (data.list && data.list.length) {
          data.list.map(item => {
            // 已完成-->医生已反馈（按照医生的反馈时间先后顺序）
            if (item.status && item.status === 'suggested') {
              this.suggestedArr.push({
                monthAge: item.month_age,
                name: item.name,
                status: this.getStatus(item.status),
                id: item._id,
                reminded_at: item.reminded_at,
                suggestion: item.suggest_content,
                operateTime: item.operateTime
              })
            }
            // 待反馈-->已测（按照用户提交的顺序）
            else if (item.status && item.status === 'tested') {
              this.testedArr.push({
                monthAge: item.month_age,
                name: item.name,
                status: this.getStatus(item.status),
                id: item._id,
                reminded_at: item.reminded_at,
                suggestion: item.suggest_content,
                operateTime: item.operateTime
              })
            }
            // 全部
            this.allDataArr.push({
              monthAge: item.month_age,
              name: item.name,
              status: this.getStatus(item.status),
              id: item._id,
              reminded_at: item.reminded_at,
              suggestion: item.suggest_content,
              operateTime: item.operateTime
            })
          })
          this.tableData = this.suggestedArr
        } else {
          this.tableData = []
        }
        this.getActiveList()
      }
    },
    sendClick(str, row) {
      this.statusText = row.status
      if (
        (this.$store.state.user.bus_role !== 'hm' && str === '发送') ||
        (this.$store.state.user.bus_role !== 'doctor' && str !== '发送')
      ) {
        return this.$message.warning('没有操作权限')
      }
      if (str === '发送') {
        this.rowInfo = row
        this.growDialogVisible = true
      } else {
        this.$router.push({
          name: 'growStatusDetail',
          params: { id: row.id },
          query: { status: this.statusText }
        })
      }
    },
    async confirmSend() {
      const { data, code } = await sendGrowth(this.rowInfo.id)
      if (code === 20000 && data.result) {
        this.$message.success('发送成功')
        this.getGrowthDetail()
        this.growDialogVisible = false
      } else this.$message.error(data.message)
    }
  },
  beforeRouteLeave(to, from, next) {
    if (
      this.statusText === '未发送' ||
      this.statusText === '待发送' ||
      this.statusText === '待测试'
    ) {
      to.meta.title = '未发送'
    }
    if (this.statusText === '待反馈') to.meta.title = '待反馈'
    if (this.statusText === '反馈已保存') to.meta.title = '填写反馈'
    if (this.statusText === '已完成') to.meta.title = '已完成'
    next()
  }
}
</script>

<style scoped lang="scss">
.see-details {
  padding: 16px 10px;
  .user-info {
    display: flex;
    line-height: 40px;
    margin-bottom: 20px;
    .item-title {
      margin-right: 10px;
      font-weight: 600;
    }
    .item-text {
      margin-right: 20px;
    }
  }
  .dot-badge {
    margin-right: 10px;
    /deep/.el-badge__content.is-dot {
      top: 8px;
    }
  }
}
</style>
