<template>
  <div class="add_notes_form">
    <!-- 添加或编辑共享备注 -->
    <el-row :gutter="24" class="header">
      <el-col :span="16" class="title">
        <slot name="header"></slot>
      </el-col>
      <el-col :span="7" class="align-right">
        <slot name="operator"></slot>
      </el-col>
    </el-row>
    <el-row class="textarea_row" v-if="showTextareaProps">
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入备注内容"
        v-model="content"
      >
      </el-input>
      <el-row class="align-center" style="margin:14px 0;">
        <el-button type="primary" size="mini" @click="submitNotes">
          保存
        </el-button>
        <el-button type="primary" size="mini" @click="cancelAdd">
          取消
        </el-button>
      </el-row>
    </el-row>
    <div v-if="!showTextareaProps" class="notes_content">
      {{ notesContent }}
    </div>
  </div>
</template>
<script>
import { addNotes, editNotes } from '@/api/sharingNotes'
export default {
  props: {
    showTextareaProps: {
      type: Boolean,
      default: true
    },
    notesContent: {
      type: String,
      default: ''
    },
    babyId: {
      type: String,
      default: ''
    },
    notesId: {
      type: String,
      default: ''
    },
    editIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      content: ''
    }
  },
  watch: {
    showTextareaProps(val) {
      if (val) {
        this.content = this.notesContent
      }
    }
  },
  methods: {
    // 添加共享备注
    submitNotes() {
      if (!this.content) {
        this.$message({
          type: 'warning',
          message: '备注不能为空'
        })
        return
      }
      if (this.notesId) {
        editNotes(this.notesId, { content: this.content }).then(res => {
          if (res.data.result) {
            this.$message.success('更新成功')
            this.content = ''
            this.$emit('editNotesSuccess', this.editIndex)
          }
        })
      } else {
        addNotes(this.babyId, { content: this.content }).then(res => {
          if (res.code === 20000) {
            this.$message.success('保存成功')
            this.content = ''
            this.$emit('submitNotesSuccess')
          }
        })
      }
    },
    cancelAdd() {
      this.$emit('cancalAdd', this.editIndex)
    }
  }
}
</script>
<style lang="scss" scoped>
.add_notes_form {
  overflow: hidden;
  margin: 16px auto;
  width: 98%;
  background: #fff;
  border: 1px solid #e8eaec;
  .header {
    background: #f5f7fa;
    border: 1px solid #e8eaec;
    border-radius: 2px;
    .title {
      margin-left: 8px;
      line-height: 40px;
      font-size: 14px;
      font-family: PingFangSC-Medium;
      color: #17233d;
      letter-spacing: 0;
    }
  }
  .textarea_row {
    margin: 10px 8px;
  }
  .notes_content {
    display: inline-block;
    margin-top: -10px;
    padding: 0 10px 10px;
    line-height: 22px;
    white-space: pre-line;
    word-break: break-all;
  }
}
</style>
