<template>
  <div style="overflow: auto;" :style="{ height: extendRecentHistoryHeight }">
    <!--聊天记录抽屉 -->
    <chat-history
      title=""
      :display.sync="displayChat"
      :inner="true"
      :width="drawerWidth"
      :mask="false"
      :direction="'left'"
    >
      <div>
        <msg-card :msg-list="messageList" />
      </div>
    </chat-history>
    <div
      v-for="history in recentHistory"
      :key="history._id"
      class="recent-history-card"
    >
      <div class="time-range">
        {{ new Date(history.start_at) | formatTime() }}
        &nbsp;到&nbsp;
        {{ new Date(history.end_at) | formatTime() }}
      </div>
      <div v-for="talk in history.list" :key="talk._id" class="talk-container">
        <div style="cursor: pointer" @click="openDetail(talk)">
          <div class="talk-title">
            <div class="text">
              <div>
                {{ new Date(talk.created_at) | formatTime('y-m-d') }}
              </div>
              <div
                v-if="talk.question_type == 'disease'"
                class="align-right margin-right-10"
              >
                <span style="margin-right:10px;">疾病护理类</span>
                {{ talk.ask_type }}
              </div>
              <div v-else class="align-right margin-right-10">
                {{ department === 'chanke' ? '其他问题' : '其他育儿类' }}
              </div>
            </div>
            <div class="icon">
              <i
                class="el-icon-arrow-right"
                :class="{ 'detail-open': talk.open }"
              />
            </div>
          </div>
          <div class="talk-content">
            <div v-if="talk.hm">
              {{ systemRole('hm', '', department) }}：{{ talk.hm.name || '' }}
            </div>
            <div v-if="talk.doctor">医生：{{ talk.doctor.name || '' }}</div>
            <div v-if="talk.dietician">
              营养师：{{ talk.dietician.name || '' }}
            </div>
            <div>状态： {{ resolveStatus(talk.status, '') }}</div>
            <div>
              {{
                talk.suggestion_type
                  ? suggestType(talk.suggestion_type)
                  : '叮嘱'
              }}
              ： {{ talk.suggestion }}
            </div>
          </div>
        </div>
        <div v-if="talk.open" class="history-detail">
          <history-detail
            :time-line-info="currentDetail"
            @gotoMessages="gotoMessages"
            :fd-ask="talk._id"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HistoryDetail from './HistoryDetail'
import ChatHistory from './ChatHistory'
import MsgCard from './MsgCard'
import { mapActions } from 'vuex'
import { getTimeLineInfo } from '@/api/chat'
import { systemRole, resolveStatus, suggestType } from '@/utils/dictionary'
import { fdAskLocation } from '@/api/message'

export default {
  name: 'CaseHistoryRecent',
  components: {
    HistoryDetail,
    ChatHistory,
    MsgCard
  },
  props: {
    recentHistory: {
      type: Array,
      default: () => []
    },
    department: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentDetail: [],
      displayChat: false,
      messageList: [],
      drawerWidth: '100%'
    }
  },
  computed: {
    extendRecentHistoryHeight() {
      if (this.$store.state.babyExtend.pageType === 'chat') {
        return '710px'
      } else {
        return '650px'
      }
    }
  },
  methods: {
    systemRole,
    resolveStatus,
    suggestType,
    // 查看聊天记录，dispatch goFdAsk
    gotoMessages(fdAsk) {
      if (this.$store.state.babyExtend.pageType === 'chat') {
        this.goFdAsk(fdAsk)
      } else {
        this.messageList = []
        this.displayChat = true
        fdAskLocation(fdAsk).then(res => {
          this.messageList = res.data.list
        })
      }
    },
    openDetail(talk) {
      if (talk.open === false) {
        getTimeLineInfo(talk._id).then(res => {
          if (res.code === 20000) {
            this.currentDetail = res.data.paths
            talk.open = true
          }
        })
      } else {
        talk.open = false
      }
    },
    ...mapActions({
      goFdAsk: 'goFdAsk'
    })
  }
}
</script>

<style scoped lang="scss">
.talk-container {
  border-bottom: 1px solid #e8eaec;
  &:last-child {
    border: none;
  }
}
.recent-history-card {
  position: relative;
  margin-top: 20px;
  .time-range {
    line-height: 30px;
    font-size: 14px;
    text-align: center;
    color: #576277;
    background: #f6f7fb;
    margin-bottom: 10px;
  }
  .talk-title {
    display: flex;
    justify-content: space-between;
    padding: 10px 0 10px 10px;
    width: 100%;
    .text {
      display: flex;
      justify-content: space-around;
      font-size: 14px;
      color: #17233d;
      flex: 5;
      border-left: 3px solid #0085ff;
      div {
        margin-left: 20px;
        flex: 1;
        &:first-child {
          flex: 1;
        }
      }
    }
    .icon {
      flex: 1;
      cursor: pointer;
    }
  }
  .talk-content {
    padding: 0 10px;
    line-height: 30px;
    font-size: 14px;
    color: #17233d;
  }
  .open-detail-button {
    position: absolute;
    right: 30px;
    bottom: 0;
  }
  .detail-open {
    transition: all 0.5s;
    -ms-transform: rotate(90deg);

    /* Internet Explorer */
    -moz-transform: rotate(90deg); /* Firefox */
    -webkit-transform: rotate(90deg); /* Safari 和 Chrome */
    -o-transform: rotate(90deg); /* Opera */
    transform: rotate(90deg);
  }
  .history-detail {
    padding-left: 10px;
  }
}
</style>
