<template>
  <div>
    <div class="detail-container">
      <monitor
        ref="monitor"
        :monitor-info="monitorInfo"
        :current-image-url-arr="curUrlList"
      />
      <div
        v-for="timePot in timeLineInfo"
        :key="timePot.id"
        class="detail-card"
      >
        <div v-if="timePot.type === 'text_answer'">
          <div class="card-title">
            <div>{{ timePot.user.name }}</div>
            <div>图文接待</div>
          </div>
          <div class="card-nurse-show">
            <div>状态：{{ resolveStatus(timePot.status, '') }}</div>
            <div>主诉：{{ timePot.main_ask }}</div>
            <div>
              {{
                timePot.suggestion_type
                  ? suggestType(timePot.suggestion_type)
                  : '叮嘱'
              }}
              ： {{ timePot.suggestion }}
            </div>
          </div>
          <div class="card-time">
            {{ new Date(timePot.created_at) | formatTime() }}
          </div>
        </div>
        <div v-else-if="timePot.type === 'user_end'">
          <div class="card-title">
            <div>用户</div>
            <div>选择结束咨询</div>
          </div>
          <div class="card-time">
            {{ new Date(timePot.created_at) | formatTime() }}
          </div>
        </div>
        <div v-else-if="timePot.type === 'system'">
          <div class="card-title">
            <div>系统</div>
            <div>{{ timePot.title }}</div>
          </div>
          <div class="card-time">
            {{ new Date(timePot.created_at) | formatTime() }}
          </div>
        </div>
        <div v-else-if="timePot.type === 'user_ask'">
          <div class="card-title">
            <div>用户</div>
            <div>健康咨询</div>
          </div>
          <div class="customer-info">
            <div>症状：{{ timePot.ask_question_desc }}</div>
            <div>
              干预史：{{ timePot.interven_desc ? timePot.interven_desc : '无' }}
            </div>
            <div>
              资料照片及视频：<span
                v-if="timePot.ask_question_report.length === 0"
                >无</span
              >
            </div>
            <el-row
              v-if="timePot.ask_question_report.length > 0"
              style="margin-top: 20px"
            >
              <el-col
                v-for="(media, subIndex) in timePot.ask_question_report"
                :key="'meida' + subIndex"
                :span="6"
              >
                <media-display-item
                  :url="media.url"
                  :media-type="media.type"
                  :url-list="timePot.ask_question_report"
                  :show-media-detail="showMediaDetail"
                />
              </el-col>
            </el-row>
          </div>
          <div class="card-time">
            {{ new Date(timePot.created_at) | formatTime() }}
          </div>
          <el-button
            type="text"
            class="review-chat"
            @click="gotoMessages(fdAsk)"
          >
            查看聊天记录
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MediaDisplayItem from '../../components/common/MediaDisplayItem'
import Monitor from '@/components/Monitor'
import { resolveStatus, suggestType } from '@/utils/dictionary'
export default {
  name: 'HistoryDetail',
  components: {
    MediaDisplayItem,
    Monitor
  },
  props: {
    timeLineInfo: {
      type: Array,
      default: () => []
    },
    fdAsk: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      monitorInfo: {},
      curUrlList: []
    }
  },
  methods: {
    resolveStatus,
    suggestType,
    gotoMessages(val) {
      this.$emit('gotoMessages', val)
    },
    // 显示图片与视频详情
    showMediaDetail(urlList, type, url) {
      this.curUrlList = urlList
        .filter(item => item.type === 'image')
        .map(({ url }) => url)
      this.monitorInfo = {
        type,
        meta: { url }
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style scoped lang="scss">
.detail-container {
  position: relative;
  overflow: auto;
  line-height: 20px;
  font-size: 12px;
  color: #999;
  .card-title {
    display: flex;
    margin-top: 10px;
    line-height: 30px;
    font-size: 14px;
    color: #576277;
    div {
      flex: 1;
      &:last-child {
        flex: 2;
      }
    }
  }
  .review-chat {
    float: right;
    margin-right: 16px;
    &::after {
      display: block;
      clear: both;
      content: '';
    }
  }
}
</style>
