<template>
  <div class="history-container">
    <el-tabs
      v-model="consultType"
      @tab-click="setType"
      type="card"
      :stretch="true"
    >
      <el-tab-pane
        size="mini"
        v-for="(item, ind) in consultTypeArr"
        :key="ind"
        :label="item.label"
        :name="item.name"
      >
      </el-tab-pane>
      <div v-if="consultType === 'disease'">
        <el-tabs
          v-model="historyType"
          type="card"
          :stretch="true"
          @tab-click="resetDrawerShow"
        >
          <el-tab-pane label="近三段（初诊 + 复诊）" name="recent">
            <case-history-recent
              :department="department"
              :recent-history="recentHistory"
            />
          </el-tab-pane>
          <el-tab-pane label="所有" name="all">
            <case-history-all
              :baby-id="babyId"
              :department="department"
              :questionType="'disease'"
              :sessionLastAsk="lastAskId"
              ref="caseHistoryAll"
            />
          </el-tab-pane>
        </el-tabs>
      </div>
      <div v-show="consultType !== 'disease'">
        <case-history-all
          :baby-id="babyId"
          :department="department"
          :questionType="consultType"
          :sessionLastAsk="lastAskId"
          :ref="consultType"
        />
      </div>
    </el-tabs>
  </div>
</template>

<script>
import CaseHistoryRecent from './components/CaseHistoryRecent'
import CaseHistoryAll from './components/CaseHistoryAll'
import { getRecentHistory } from '@/api/chat'
import { mapGetters } from 'vuex'
export default {
  name: 'CaseHistory',
  components: {
    CaseHistoryRecent,
    CaseHistoryAll
  },
  props: {
    babyId: {
      type: String,
      default: ''
    },
    department: {
      type: String,
      default: ''
    },
    lastAskId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      consultType: 'all',
      consultTypeArr: [
        {
          name: 'all',
          label: '全部'
        },
        {
          name: 'disease',
          label: '疾病护理类'
        },
        {
          name: 'other',
          label: '其他问题'
        }
      ],
      displayChat: false,
      messageList: [],
      drawerWidth: '100%',
      historyType: 'recent',
      recentHistory: []
    }
  },
  computed: {
    ...mapGetters(['babyExtendTab']),
    isRecentHistory: function() {
      // 当主选项是 咨询历史时：
      if (this.babyExtendTab && this.babyExtendTab === 'consultHistory') {
        if (this.consultType === 'disease') {
          // 当前咨询历史的类型为：疾病护理类
          // 设置选中最近三段
          const currentFdask = this.lastAskId || ''
          if (!currentFdask) {
            return false
          } else {
            return true
          }
        }
      }
      return false
    }
  },
  watch: {
    isRecentHistory(val) {
      if (val) {
        this.historyType = 'recent'
        this.recentHistory = []
        const params = {
          last_three: 1,
          fd_ask: this.lastAskId
        }
        this.getRecentHistory(params)
      } else {
        this.recentHistory = []
      }
    },
    department() {
      this.consultTypeArr[2].label =
        this.department === 'chanke' ? '其他问题' : '其他育儿类'
    }
  },
  methods: {
    setType() {
      console.log(this.consultType)
    },
    // 点击tab时，全部咨询历史中的抽屉状态复位
    resetDrawerShow() {
      this.$refs.caseHistoryAll.display = false
    },
    getRecentHistory(params) {
      // 获取近三段咨询历史：
      getRecentHistory(params)
        .then(res => {
          this.recentHistory = res.data
          const a = this.recentHistory.map(history => {
            history.list = history.list.map(talk => {
              talk = {
                ...talk,
                open: false
              }
              return talk
            })
            return history
          })
          this.recentHistory = a
        })
        .catch(error => {
          console.log('获取最近的三段咨询记录' + error)
        })
    }
  }
}
</script>
