<template>
  <div
    class="baby-box cursor-pointer"
    :class="
      activedBaby.family_member &&
      babyDetail.family_member &&
      babyDetail.family_member._id === activedBaby.family_member._id
        ? 'active_baby'
        : ''
    "
    @click="handleBaby(babyDetail)"
  >
    <div v-if="babyDetail.is_asking" class="consulting">
      咨询中
    </div>
    <el-row class="baby-collapse">
      <el-col :span="pathName.indexOf('diagnose') !== -1 ? 6 : 3">
        <div
          class="baby-avatar"
          :style="{
            backgroundImage: `url(${babyDetail.family_member.avatar})`
          }"
        ></div>
      </el-col>
      <el-col :span="17" class="clearfix baby-collapse-right">
        <p class="float-left name">{{ babyDetail.family_member.name }}</p>
        <p class="float-right">
          <my-copy :copyKey="babyDetail.family_member.name"></my-copy>
          <i
            v-if="!department"
            class="cursor-pointer margin-left-10"
            :class="!showDetail ? 'el-icon-arrow-right' : 'el-icon-arrow-down'"
            @click="showDetailFun"
          ></i>
        </p>
      </el-col>
    </el-row>
    <div class="baby-info-box">
      <el-row class="clearfix">
        <el-col :span="11" class="baby-id">
          <span>ID：</span>
          <span class="span-val">{{ babyDetail.family_member.id }}</span>
        </el-col>
        <el-col
          :span="9"
          :style="
            pathName.indexOf('diagnose') !== -1
              ? 'margin-left:5px'
              : 'margin-left:0px'
          "
        >
          <my-copy
            class="float-right"
            :copyKey="babyDetail.family_member.id"
          ></my-copy>
        </el-col>
      </el-row>
      <!-- 套餐信息 -->
      <ServiceInfo
        :serviceInfo="babyDetail.fd_contract_info"
        :department="babyDetail.family_member.type"
        :fdProduct="babyDetail.fd_product"
      ></ServiceInfo>
      <!-- 详细显示信息 -->
      <div v-show="showDetail">
        <BabyCard
          :isShow="showDetail"
          :department="babyDetail.family_member.type"
          :babyDetail="babyDetail"
        ></BabyCard>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import BabyCard from '../components/BabyCard'
import ServiceInfo from '../components/ServiceInfo'
import { localSave, localRead } from '@/utils/index.js'
export default {
  components: {
    BabyCard,
    ServiceInfo
  },
  props: {
    babyDetail: {
      type: Object,
      default: () => {}
    },
    department: {
      type: String,
      default: ''
    },
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showDetail: false,
      pathName: window.location.pathname
    }
  },
  watch: {
    'babyDetail.is_asking'(val) {
      if (val === true) {
        this.showDetail = true
      }
    },
    isShow(val) {
      this.showDetail = val
    },
    activedBabyId(newVal, oldVal) {
      if (oldVal === this.babyDetail.family_member._id) {
        this.saveDataToLocal(oldVal, this.showDetail)
      }
      const params = localRead('babyInfo' + newVal)
      if (params) {
        const paramsJson = JSON.parse(params)
        this.showDetail = paramsJson.showDetail
      } else {
        this.showDetail = false
      }
    }
  },
  computed: {
    ...mapGetters(['activedBaby', 'activedBabyId'])
  },
  created() {
    if (this.babyDetail.is_asking) {
      this.showDetail = true
    }
    this.showDetail = this.isShow
  },
  methods: {
    saveDataToLocal(old, flag) {
      const bId = old
      // 缓存数据
      const params = {
        babyId: bId,
        showDetail: flag,
        remark: ''
      }
      const paramsString = JSON.stringify(params)
      localSave('babyInfo' + bId, paramsString)
    },
    showDetailFun() {
      this.showDetail = !this.showDetail
    },
    handleBaby(customer) {
      if (!this.department) {
        const obj = {
          clickTag: 'baby',
          session_id: customer.session_id,
          userId: '',
          babyId: customer.family_member ? customer.family_member._id : ''
        }
        this.$store.dispatch('setCustomerInfo', obj)
      }
    }
  }
}
</script>
<style lang="scss" scope>
.baby-box {
  padding: 10px 0 0;
  line-height: 20px;
  font-size: 14px;
  color: #576277;
}
.baby-avatar {
  width: 38px;
  height: 38px;
  background-position: center center;
  background-size: cover;
  border-radius: 50%;
}
.consulting {
  padding-left: 5px;
  width: 50px;
  font-size: 12px;
  color: #fff;
  background-color: #ff97a0;
  margin-bottom: 6px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.active_baby {
  background-color: #f6f7fb;
}
.baby-collapse-right {
  line-height: 40px;
}
.baby-id {
  word-break: break-all;
}
.baby-collapse,
.baby-info-box {
  padding: 2px 6px 6px;
}
</style>
