<template>
  <div>
    <div class="clearfix">
      <span class="title">通话录音</span>
      <span class="float-right">{{ msgtime }}</span>
    </div>
    <div
      ref="wrap"
      class="x-audio-wrap"
      style="min-width:250px;max-width:500px;"
      :style="'width:' + width + 'px'"
    >
      <div class="x-sector" @click="play">
        <img class="sound-img" :src="soundImg" />
      </div>
      <!-- 进度条 -->
      <div class="progress_box">
        <el-slider
          v-model="progressValue"
          :format-tooltip="formatTooltip"
          @change="changeProgressValue"
        />
      </div>
      <div class="x-time">{{ duration && showDuration ? duration : text }}</div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'VoiceCard',
  props: {
    src: {
      type: String,
      default: ''
    },
    msgtime: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: ''
    },
    vduration: {
      type: Number,
      default: 0
    },
    showDuration: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      width: '',
      progressValue: 0,
      audio: null,
      animate: false,
      timer: null,
      duration: null,
      soundImg: require('@/assets/play.png'),
      slideLeft: '0',
      oldvalue: 0,
      audioTime: '',
      is_moving: false,
      sb_bkx: 0,
      sb_bky: 0
    }
  },
  computed: {
    ...mapGetters(['activedBaby'])
  },
  watch: {
    animate() {
      if (this.animate === false) {
        this.soundImg = require('@/assets/play.png')
      } else {
        this.soundImg = require('@/assets/pause.png')
      }
    },
    activedBaby() {
      this.audio.pause()
    }
  },
  mounted() {
    this.audio = new Audio()
    this.audio.src = this.src
    this.width = (this.vduration / 60000) * 75 - 25
    this.audio.setAttribute('msgtime', this.msgtime)
    this.audio.addEventListener('canplaythrough', () => {})
    this.audio.onplay = () => {
      this.timer = setInterval(res => {
        this.updateProgress()
      }, 300)
      this.animate = true
    }
    this.audio.onpause = () => {
      this.animate = false
      this.timer && clearInterval(this.timer)
    }
    this.audio.onended = () => {
      this.animate = false
      this.timer && clearInterval(this.timer)
    }
    window.audioList.push(this.audio) // 所有实例加入全局变量
  },
  methods: {
    formatTooltip() {
      let audioCurrentTime = 0
      if (this.audio && this.audio.duration) {
        audioCurrentTime = this.realFormatSecond(
          (this.audio.duration * this.progressValue) / 100
        )
      }
      return audioCurrentTime
    },
    changeProgressValue(val) {
      this.play()
      this.audio.currentTime = parseInt((this.audio.duration * val) / 100)
    },

    play() {
      window.audioList.forEach((audio, index) => {
        // 开始前先关闭所有可能正在运行的实例
        const msgtime = audio.getAttribute('msgtime')
        const currentMsgtime = this.audio.getAttribute('msgtime')
        if (msgtime !== currentMsgtime) {
          audio.pause()
        }
      })
      if (this.audio.paused) {
        this.audio.play()
      } else {
        this.audio.pause()
      }
    },
    updateProgress() {
      const value = Math.round(
        (Math.floor(this.audio.currentTime) / Math.floor(this.audio.duration)) *
          100
      ) // 当前时间/总长 再乘以一个100变成百分数
      if (value - this.oldvalue > 0) {
        this.oldvalue = value
        this.progressValue = value // 0.907 ：进度条div的宽度除以背景img的宽度，如果他们俩的长度一样，就直接value就可以。
      }
    },
    // 格式化秒
    realFormatSecond(second) {
      var secondType = typeof second
      if (secondType === 'number' || secondType === 'string') {
        second = parseInt(second)
        var mimute = Math.floor(second / 60)
        second = second - mimute * 60
        return ('0' + mimute).slice(-2) + ':' + ('0' + second).slice(-2)
      } else {
        return '00:00'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
span.title {
  float: left;
  font-size: 14px;
  font-family: PingFangSC-Regular;
  color: #333;
  letter-spacing: 0;
}
.x-audio-wrap {
  position: relative;
  padding-right: 10px;
  height: 30px;
  background: #fff;
  border: 1px solid #e8eaec;
  border-radius: 4px;
  .x-time {
    position: absolute;
    top: 0;
    right: 3px;
    font-size: 12px;
    color: #999;
  }
}
.progress_box {
  position: relative;
  display: inline-block;
  margin-top: -6px;
  margin-left: 18px;
  width: 89%;
  height: 100%;
}
@media (min-width: 1280px) {
  .progress_box {
    width: 83%;
  }
}
@media (min-width: 1480px) {
  .progress_box {
    width: 85%;
  }
}
@media (min-width: 1640px) {
  .progress_box {
    width: 89%;
  }
}
.my-progress {
  position: absolute;
  top: 11px;
  width: 100%;
  z-index: 200;
}
.my_slide {
  position: absolute;
  top: 4px;
  width: 3px;
  height: 20px;
  background: #ff4e4e;
  z-index: 201;
  cursor: pointer;
}
.x-sector {
  position: relative;
  top: 3px;
  left: 3px;
  display: inline-block;
  width: 10%;
  max-width: 22px;
  font-size: 14px;
  img.sound-img {
    display: inline-block;
    margin-left: 3px;
    width: 22px;
    height: 22px;
    color: red;
  }
}
</style>
