<template>
  <div class="share-notes">
    <SharingForm
      ref="sharingForm"
      @showAddForm="addVisibleTrue"
      @sharingFormChange="sharingFormChange"
      :babyId="babyId"
    ></SharingForm>
    <AddSharingNotes
      ref="addSharingNotes"
      v-show="addVisible"
      :babyId="babyId"
      @submitNotesSuccess="submitNotesSuccess"
      @cancalAdd="addVisibleFalse"
    >
      <template slot="header">
        {{ title }}
      </template>
    </AddSharingNotes>
    <!-- 滚动加载备注列表 -->
    <el-row v-if="notesList && notesList.length">
      <scroll-box :box-height="addVisible ? 300 : 560">
        <ul v-infinite-scroll="loadMore" infinite-scroll-immediate="disabled">
          <li
            v-for="(item, index) in notesList"
            :key="index"
            style="line-height: 30px;"
          >
            <AddSharingNotes
              :babyId="babyId"
              @editNotesSuccess="editNotesSuccess"
              @cancalAdd="addVisibleFalse"
              :showTextareaProps="showTextareaArr[index].show"
              :notesContent="item.content"
              :notesId="item._id"
              :editIndex="index"
            >
              <template slot="header">
                {{
                  item.admin_user && item.admin_user.name
                    ? item.admin_user.name
                    : ''
                }}
                {{ formatTime(item.created_at) }}
              </template>
              <template
                v-if="
                  item.admin_user &&
                    item.admin_user._id &&
                    item.admin_user._id === userInfo.id
                "
                slot="operator"
              >
                <el-button type="text" @click="showEdit(index)">
                  修改
                </el-button>
                <el-button type="text" @click="showDelNotes(item)"
                  >删除</el-button
                >
              </template>
            </AddSharingNotes>
          </li>
        </ul>
      </scroll-box>
    </el-row>
    <el-row v-if="!notesList || notesList.length === 0" class="no-data">
      暂无数据
    </el-row>
    <el-dialog
      title="删除提示"
      :visible.sync="delNotesDialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <span>删除后该条数据将从共享备注列表中移除，请确认是否继续删除</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="delNotesFun">删除</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import SharingForm from './components/SharingForm'
import AddSharingNotes from './components/AddSharingNotes'
import { getNotesList, delNotesApi } from '@/api/sharingNotes'
import { mapGetters } from 'vuex'

import ScrollBox from '@/components/ScrollBox'

export default {
  computed: {
    ...mapGetters(['bus_role', 'userInfo']),
    disabled() {
      return this.loading || this.noMore
    }
  },
  components: {
    SharingForm,
    ScrollBox,
    AddSharingNotes
  },
  props: {
    babyId: {
      type: String,
      default: ''
    }
  },
  watch: {
    'defaultSearcheParams.count'() {
      this.pageSizes =
        this.defaultSearcheParams.count % this.defaultSearcheParams.pageSize ===
        0
          ? this.defaultSearcheParams.count / this.defaultSearcheParams.pageSize
          : this.defaultSearcheParams.count /
              this.defaultSearcheParams.pageSize +
            1
    },
    babyId() {
      this.notesList = []
      this.loadMoreFlag = false
      // 宝宝id变化时，查询共享备注的bus_role参数重置为健康管理师
      this.$refs.sharingForm.sharingForm = {
        content: '',
        name: '',
        createdAt: '',
        bus_role: 'hm'
      }
      // 宝宝id变化时，新增备注的状态重置
      this.$refs.addSharingNotes.content = ''
      this.addVisible = false
      this.defaultSearcheParams = {
        currentPage: 1, // 当前页数
        pageSize: 10, //每页10条
        count: 0 // 总条数
      }
      this.getNotesList()
    }
  },
  data() {
    return {
      notesId: '',
      delNotesDialogVisible: false,
      addVisible: false,
      notesList: [],
      title: '添加备注',
      defaultSearcheParams: {
        currentPage: 1, // 当前页数
        pageSize: 10, //每页10条
        count: 0 // 总条数
      },
      pageSizes: 0, //总共多少页
      loadMoreFlag: false,
      showTextareaArr: [],
      SearcheParams: {
        content: '',
        name: '',
        beginTime: '',
        endTime: '',
        bus_role: 'hm'
      },
      // 加载状态
      loading: false,
      noMore: ''
    }
  },
  mounted() {
    this.getNotesList()
  },
  methods: {
    handleClose() {
      this.delNotesDialogVisible = false
    },
    addVisibleTrue() {
      this.addVisible = true
    },
    addVisibleFalse(index) {
      this.addVisible = false
      if (index || index === 0) {
        this.$set(this.showTextareaArr[index], 'show', false)
        this.$forceUpdate()
      }
    },
    showDelNotes(item) {
      this.delNotesDialogVisible = true
      this.notesId = item._id
    },
    delNotesFun() {
      delNotesApi(this.notesId).then(res => {
        if (res.data.result) {
          this.handleClose()
          this.notesId = ''
          this.notesList = []
          this.loadMoreFlag = false
          this.getNotesList()
        }
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result ? '删除成功' : '删除失败'
        })
      })
    },
    sharingFormChange(val) {
      this.SearcheParams = val
      this.submitNotesSuccess()
    },
    async getNotesList() {
      if (!this.babyId) {
        return
      }
      this.SearcheParams = Object.assign(
        this.$refs.sharingForm.sharingForm,
        this.defaultSearcheParams
      )
      this.SearcheParams.beginTime =
        this.SearcheParams.createdAt && this.SearcheParams.createdAt.length
          ? this.SearcheParams.createdAt[0]
          : ''
      this.SearcheParams.endTime =
        this.SearcheParams.createdAt && this.SearcheParams.createdAt.length
          ? this.SearcheParams.createdAt[1]
          : ''
      await getNotesList(this.babyId, this.SearcheParams).then(res => {
        if (res.data.list) {
          if (this.loadMoreFlag) {
            this.notesList = this.notesList.concat(res.data.list)
          } else {
            this.notesList = res.data.list
          }
          for (let i = 0; i < this.notesList.length; i++) {
            if (this.showTextareaArr[i] && this.showTextareaArr[i].show) {
              this.$set(this.showTextareaArr[i], 'show', false)
            } else {
              this.showTextareaArr[i] = { show: false }
            }
          }
          this.defaultSearcheParams.count = res.data.count
          this.defaultSearcheParams.currentPage = res.data.currentPage
        }
      })
    },
    showEdit(index) {
      this.$set(this.showTextareaArr[index], 'show', true)
      this.$forceUpdate()
    },
    editNotesSuccess(index) {
      this.submitNotesSuccess()
    },
    submitNotesSuccess() {
      this.notesList = []
      this.loadMoreFlag = false
      this.defaultSearcheParams = {
        currentPage: 1, // 当前页数
        pageSize: 10, //每页10条
        count: 0 // 总条数
      }
      this.getNotesList()
      this.addVisible = false
    },
    // 加载更多
    loadMore() {
      // 是否当前page不是最后一页
      if (this.defaultSearcheParams.currentPage <= this.pageSizes) {
        this.loading = true
        this.defaultSearcheParams.currentPage++
        this.loadMoreFlag = true
        this.getNotesList()
        this.loading = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.share-notes {
  .no-data {
    margin: 50px auto;
    text-align: center;
  }
}
</style>
