<template>
  <div>
    <div
      v-if="showCard === false"
      style="font-size: 12px;color: #576277;cursor: pointer;"
      @click="showCard = true"
    >
      {{ title }}
    </div>
    <div
      v-else
      style="font-size: 12px;color: #576277;cursor: pointer;"
      @click="showCard = false"
    >
      关闭查看
    </div>
    <div v-show="showCard">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'OpenCard',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      showCard: false
    }
  }
}
</script>
