<template>
  <div class="bout-card" @click="goMessageDetail">
    <el-row v-if="title">
      <h5 class="card_title" style="min-width:220px;">{{ title }}</h5>
      <p class="divider"></p>
    </el-row>
    <el-row
      v-for="(boutItem, index) in boutCard"
      :key="'boutItem' + index"
      :gutter="24"
    >
      <el-col :span="24" v-if="boutItem.show">
        <div v-if="boutItem.type === 'confirm_text'">
          <p class="confirm_text">
            {{ boutItem.content }}
          </p>
        </div>
        <div v-if="boutItem.type === 'ask_type'">
          <p class="divider"></p>
          <p class="confirm_text">
            <span> {{ boutItem.content }}</span>
            <span v-show="boutItem.showTime" class="float-right create_time">
              {{ boutItem.time }}
            </span>
          </p>
          <p class="divider"></p>
        </div>
        <div v-if="boutItem.type === 'text'">
          <h5 class="card_title">{{ boutItem.title }}</h5>
          <div class="text-wrap">{{ boutItem.content }}</div>
        </div>
        <el-row type="flex" v-if="boutItem.type === 'btn'" justify="center">
          <el-button
            size="mini"
            type="info"
            class="notic_disabled_btn"
            :disabled="true"
          >
            已确认
          </el-button>
        </el-row>
        <div v-if="boutItem.type === 'voice'">
          <my-audio
            :msgtime="msgTime"
            :src="boutItem.voice.url"
            :vduration="boutItem.voice.duration"
          />
        </div>
        <!-- 问题描述相关图片及视频 -->
        <div
          class="data"
          v-if="boutItem.dataArr && boutItem.dataArr.length > 0"
        >
          <div
            v-for="(item, subIndex) in boutItem.dataArr"
            :key="subIndex"
            class="image-and-video"
          >
            <img
              v-if="item.type === 'image'"
              :src="item.url"
              alt=""
              width="50px"
              @click="showMediaDetail(item.url, item.type, boutItem.dataArr)"
            />
            <div v-else-if="item.type === 'video'" class="video-container">
              <!-- 视频消息 -->
              <div class="VideoContent">
                <img
                  :src="
                    item.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                  "
                  :data-src="
                    item.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                  "
                  alt=""
                />
                <i
                  class="el-icon-video-play"
                  @click="showMediaDetail(item.url, 'video', [])"
                />
              </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import Vue from 'vue'
const genderFilter = Vue.filter('gender')
export default {
  name: 'bout-card',
  components: {},
  props: {
    msgTime: {
      type: String,
      default: ''
    },
    boutCard: {
      type: Array,
      default: () => []
    },
    hasInterven: {
      type: Boolean,
      default: false
    },
    intervenDesc: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentMediaType: '',
      currentMediaUrl: '',
      dialogVisible: false
    }
  },

  methods: {
    genderFilter,
    goMessageDetail() {
      this.$emit('goMsgDetail')
    },
    showMediaDetail(meidiaUrl, mediaType, Arr) {
      const imageArr = []
      for (let i = 0; i < Arr.length; i++) {
        const item = Arr[i]
        if (item.type === 'image' && item.url) {
          imageArr.push(item.url)
        }
      }
      const params = {
        mediaType: mediaType,
        meidiaUrl: meidiaUrl,
        list: imageArr
      }
      this.$emit('showDetail', params)
    },
    closeVideoDialog() {
      if (this.currentMediaType === 'video') {
        const myVideo = document.getElementById('myVideo')
        myVideo.pause()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.confirm_text {
  font-size: 14px;
  font-weight: bold;
  color: #f98611;
}
.card_title {
  font-size: 14px;
}
.create_time {
  font-size: 12px;
  font-weight: normal;
  color: #999;
}
.bout-card {
  width: 100%;
  font-size: 15px;
  text-align: left;
  .VideoContent {
    position: relative;
    max-width: 350px;
    img {
      max-width: 290px;
      max-height: 260px;
    }
    i.el-icon-video-play {
      position: absolute;
      top: 27%;
      left: 8.5%;
      font-size: 39px;
      color: #fff;
      z-index: 1000;
      &:hover {
        cursor: pointer;
      }
    }
  }
  .data {
    .image-and-video {
      display: inline-block;
      margin-left: 10px;
      width: 75px;
      height: 75px;
      border-radius: 6px;
      &:nth-child(7n + 1) {
        margin-left: 0;
      }
      .el-icon-video-play {
        top: 21%;
        left: 23%;
      }
    }
    img,
    video {
      position: relative;
      width: 75px;
      height: 75px;
      cursor: pointer;
    }
  }
  .el-dialog__body img {
    width: 100px;
    cursor: pointer;
  }
  .video-container {
    position: relative;
    .video-mask {
      position: absolute;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 75px;
      height: 75px;
      background-color: transparent;
      cursor: pointer;
      span {
        font-size: 20px;
        color: #fff;
        &:hover {
          color: #2d8cf0;
        }
      }
    }
  }
}
</style>
