<template>
  <div class="extend_wrap">
    <el-tabs v-model="activeInfo" :stretch="true">
      <el-tab-pane v-if="pageType !== 'chat'" label="基本信息" name="basicInfo">
        <basic-info style="padding:0 10px;" :baby-id="babyId" />
      </el-tab-pane>
      <!-- 产科无内部共享备注 -->
      <el-tab-pane
        v-if="department === 'erke'"
        label="内部共享备注"
        name="sharingNotes"
      >
        <sharing-notes :baby-id="babyId" />
      </el-tab-pane>
      <el-tab-pane
        name="growsList"
        v-if="department === 'erke' && ifHasServer('baby_growth')"
      >
        <span slot="label">
          <el-badge :is-dot="count" class="grow-dot">
            发育量表
          </el-badge>
        </span>
        <scroll-box v-if="activeInfo === 'growsList'" :box-height="810">
          <see-detail
            v-show="!isSee"
            :member="babyId"
            :page-type="'chat'"
            @setLookStatus="setLookStatus"
          />
          <status-detail
            v-if="isSee"
            :showBtn="isSee"
            @return="setLookStatus(false)"
          />
        </scroll-box>
      </el-tab-pane>
      <el-tab-pane label="健康档案" name="healthHistory">
        <scroll-box v-if="activeInfo === 'healthHistory'" :box-height="810">
          <!-- 儿科 -->
          <file-detail
            v-if="department === 'erke'"
            style="padding:0 10px;"
            :baby-id="babyId"
            :isChatPage="true"
          />
          <!-- 产科 -->
          <healthfileDetail
            v-if="department === 'chanke'"
            :pageType="'chat'"
            :department="department"
            :memberId="babyId"
          >
          </healthfileDetail>
        </scroll-box>
      </el-tab-pane>
      <el-tab-pane label="咨询历史" name="consultHistory">
        <case-history
          v-if="activeInfo === 'consultHistory'"
          :department="department"
          :baby-id="babyId"
          :lastAskId="lastAskId"
        />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import basicInfo from './basicInfo'
import CaseHistory from './caseHistory'
import sharingNotes from './sharingNotes'
import FileDetail from '@/views/medical-service/health-file/fileDetail'
import healthfileDetail from '@/views/obstetrics-doctor/healthFile/detail'
import SeeDetail from '@/views/medical-service/grow-table/seeDetails'
import StatusDetail from '@/views/medical-service/grow-table/statusDetail'
import { mapGetters } from 'vuex'
import { ifHasServer } from '@/utils/chat/index'
import { getSendCount } from '@/api/healthManagement'
import { getBabyDetailById } from '@/api/userInfo'
import ScrollBox from '@/components/ScrollBox'

export default {
  components: {
    basicInfo,
    CaseHistory,
    SeeDetail,
    StatusDetail,
    FileDetail,
    healthfileDetail,
    sharingNotes,
    ScrollBox
  },
  props: {
    babyId: {
      type: String,
      default: ''
    },
    pageType: {
      type: String,
      default: 'chat'
    }
  },
  data() {
    return {
      isSee: false,
      count: false,
      userId: '',
      babyInfo: {},
      lastAskId: '',
      activeInfo: '',
      department: ''
    }
  },
  computed: {
    ...mapGetters(['lastAsk', 'userInfo'])
  },
  watch: {
    babyId(val) {
      this.getDetail()
    },
    activeInfo(val) {
      this.$store.commit('babyExtend/SET_ACTIVEINFO', val)
    }
  },
  created() {
    this.getDetail()
  },
  methods: {
    ifHasServer,
    async getDetail() {
      // 获取详情
      this.department = ''
      if (this.babyId) {
        this.getSendCount(this.babyId)
        await getBabyDetailById(this.babyId).then(res => {
          this.babyInfo = res.data
          if (res.data.length) {
            this.department = this.babyInfo[0].type
            this.lastAskId = res.data[0].last_ask
              ? res.data[0].last_ask
              : this.lastAsk
              ? this.lastAsk._id
              : ''
          }
        })
      }
      // 根据不同科室及页面类型选中不同的tab
      let activeInfo = this.pageType === 'chat' ? 'sharingNotes' : 'basicInfo'
      if (this.department === 'chanke' && this.pageType === 'chat') {
        activeInfo = 'healthHistory'
      }
      this.activeInfo = activeInfo
      this.$store.commit('babyExtend/SET_PAGETYPE', this.pageType)
      this.$store.commit('babyExtend/SET_ACTIVEINFO', this.activeInfo)
    },

    async getSendCount(id) {
      const { data } = await getSendCount({ baby: id })
      this.count = data.count ? true : false
    },
    setLookStatus(flag) {
      this.isSee = flag === false ? flag : true
    }
  }
}
</script>
<style lang="scss" scope>
.extend_wrap {
  .el-tabs__nav-wrap {
    padding: 1px auto;
  }
}
.grow-dot {
  .is-dot {
    position: absolute;
    top: 19px;
    right: -3px !important;
  }
}
</style>
