<template>
  <div class="drawer">
    <div :class="maskClass" @click="closeByMask" />
    <div :class="mainClass" :style="mainStyle" class="main">
      <div class="drawer-head">
        <span>&nbsp;&nbsp;&nbsp;聊天记录</span>
        <span
          v-show="closable"
          class="close-btn el-icon-close"
          @click="closeByButton"
        />
      </div>
      <div class="drawer-body">
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // 是否打开
    display: {
      type: Boolean
    },

    // 标题
    title: {
      type: String,
      default: '标题'
    },

    // 是否显示关闭按钮
    closable: {
      type: Boolean,
      default: true
    },

    // 是否显示遮罩
    mask: {
      type: Boolean,
      default: true
    },

    // 是否点击遮罩关闭
    maskClosable: {
      type: Boolean,
      default: true
    },

    // 宽度
    width: {
      type: String,
      default: '400px'
    },

    // 是否在父级元素中打开
    inner: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    maskClass: function() {
      return {
        'mask-show': this.mask && this.display,
        'mask-hide': !(this.mask && this.display),
        inner: this.inner
      }
    },
    mainClass: function() {
      return {
        'main-show': this.display,
        'main-hide': !this.display,
        inner: this.inner
      }
    },
    mainStyle: function() {
      return {
        width: this.width,
        right: this.display ? '0' : `+${this.width}`,
        borderLeft: this.mask ? 'none' : '1px solid #eee'
      }
    }
  },
  mounted() {},
  methods: {
    closeByMask() {
      this.maskClosable && this.$emit('update:display', false)
    },
    closeByButton() {
      this.$emit('update:display', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.drawer {
  /* 遮罩 */
  .mask-show {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 1;
    z-index: 10;
    transition: opacity 0.5s;
  }
  .mask-hide {
    opacity: 0;
    transition: opacity 0.5s;
  }

  /* 滑块 */
  .main {
    position: fixed;
    top: 0;
    overflow: auto !important;
    height: 780px;
    background: #fff;
    z-index: 10;
    transition: all 0.5s;
  }
  .main-show {
    opacity: 1;
  }
  .main-hide {
    opacity: 0;
  }

  /* 某个元素内部显示 */
  .inner {
    position: absolute;
    top: -10px;
  }

  /* 其他样式 */
  .drawer-head {
    display: flex;
    justify-content: space-between;
    padding: 0 15px 0 30px;
    height: 45px;
    line-height: 45px;
    font-size: 14px;
    font-weight: bold;
    color: #409eff;
    .close-btn {
      display: flex;
      align-items: center;
      padding-left: 20px;
      height: 100%;
      color: #5a5e66;
      cursor: pointer;
    }
  }
  .drawer-body {
    font-size: 14px;
  }
}
</style>
