<template>
  <div>
    <div v-for="history in rangeHistory" :key="history._id" class="case-drawer">
      <div class="time-range">
        {{ new Date(history.start_at) | formatTime() }}
        到
        {{ new Date(history.end_at) | formatTime() }}
      </div>
      <div v-for="talk in history.list" :key="talk._id">
        <div style="cursor: pointer" @click="openDetail(talk)">
          <div class="talk-title">
            <div class="text">
              <div>
                {{ new Date(talk.created_at) | formatTime('y-m-d') }}
              </div>
              <div
                v-if="talk.question_type == 'disease'"
                class="align-right margin-right-10"
              >
                <span style="margin-right:10px;">疾病护理类</span>
                {{ talk.ask_type }}
              </div>
              <div v-else class="align-right margin-right-10">
                {{ department === 'chanke' ? '其他问题' : '其他育儿类' }}
              </div>
            </div>
            <div class="icon">
              <i
                class="el-icon-arrow-right"
                :class="{ 'detail-open': talk.open }"
              />
            </div>
          </div>
          <div class="talk-content">
            <div v-if="talk.hm">
              {{ systemRole('hm', '', department) }}：{{ talk.hm.name }}
            </div>
            <div v-if="talk.doctor">医生：{{ talk.doctor.name }}</div>
            <div v-if="talk.dietician">营养师： {{ talk.dietician.name }}</div>
            <div>状态： {{ resolveStatus(talk.status, '') }}</div>
            <div>
              {{
                talk.suggestion_type
                  ? suggestType(talk.suggestion_type)
                  : '叮嘱'
              }}
              ： {{ talk.suggestion }}
            </div>
          </div>
        </div>
        <div v-if="talk.open" class="history-detail">
          <history-detail
            :time-line-info="currentDetail"
            :department="department"
            @gotoMessages="gotoMessages"
            :fd-ask="talk._id"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HistoryDetail from './HistoryDetail'
import { getTimeLineInfo } from '@/api/chat'
import { mapActions } from 'vuex'
import { systemRole, resolveStatus, suggestType } from '@/utils/dictionary'

export default {
  name: 'RangeHistory',
  components: {
    HistoryDetail
  },
  props: {
    rangeHistory: {
      type: Array,
      default: () => []
    },
    department: {
      type: String,
      default: ''
    }
  },
  methods: {
    suggestType,
    systemRole,
    resolveStatus,
    ...mapActions({
      goFdAsk: 'goFdAsk'
    }),
    // 打开抽屉里的咨询轨迹详情
    openDetail(talk) {
      if (talk.open === false) {
        getTimeLineInfo(talk._id).then(res => {
          if (res.code === 20000) {
            this.currentDetail = res.data.paths
            talk.open = true
          }
        })
      } else {
        talk.open = false
      }
    },
    gotoMessages(fdAsk) {
      if (this.$store.state.babyExtend.pageType === 'chat') {
        this.goFdAsk(fdAsk)
      } else {
        this.$emit('openMessageList', fdAsk)
      }
    }
  }
}
</script>

<style scoped lang="scss">
.case-drawer {
  position: relative;
  .history-detail {
    padding-bottom: 30px;
    padding-left: 30px;
  }
  .talk-title {
    padding-left: 30px;
  }
  .talk-content {
    padding-right: 10px;
    padding-left: 30px;
  }
}
.time-range {
  padding-left: 30px;
  line-height: 30px;
  font-size: 14px;
  color: #576277;
  background: #f6f7fb;
  margin-bottom: 10px;
}
.detail-open {
  transition: all 0.5s;
  -ms-transform: rotate(90deg);

  /* Internet Explorer */
  -moz-transform: rotate(90deg); /* Firefox */
  -webkit-transform: rotate(90deg); /* Safari 和 Chrome */
  -o-transform: rotate(90deg); /* Opera */
  transform: rotate(90deg);
}
.talk-title {
  display: flex;
  justify-content: space-between;
  padding: 10px 0 10px 10px;
  width: 100%;
  .text {
    display: flex;
    justify-content: space-around;
    font-size: 14px;
    color: #17233d;
    flex: 7;
    border-left: 3px solid #0085ff;
    div {
      margin-left: 10px;
      flex: 1;
      &:first-child {
        flex: 1;
      }
    }
  }
  .icon {
    flex: 1;
    cursor: pointer;
  }
}
.talk-content {
  position: relative;
  padding: 0 10px;
  line-height: 30px;
  font-size: 14px;
  color: #17233d;
  .review-chat {
    bottom: 12px;
    float: right;
    margin-right: 16px;
    &::after {
      display: block;
      clear: both;
      content: '';
    }
  }
}
</style>
