<template>
  <div class="sharing_form">
    <el-form label-position="top" ref="sharingForm" :model="sharingForm">
      <el-form-item label="关键字搜索">
        <el-input
          size="mini"
          @input="sharingFormChange"
          v-model="sharingForm.content"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item label="人员搜索">
        <el-input
          clearable
          size="mini"
          @input="sharingFormChange"
          v-model="sharingForm.name"
        ></el-input>
      </el-form-item>
      <el-form-item label="备注时间">
        <el-date-picker
          size="mini"
          clearable
          @input="sharingFormChange"
          v-model="sharingForm.createdAt"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-radio-group
        v-model="sharingForm.bus_role"
        @input="busRoleChange"
        size="mini"
      >
        <el-radio-button label="hm">健康管理师</el-radio-button>
        <el-radio-button label="doctor">医生</el-radio-button>
        <el-radio-button label="dietician">营养师</el-radio-button>
        <el-radio-button label="">全部</el-radio-button>
      </el-radio-group>
      <el-button
        type="text"
        class="add_sharing_notes_btn"
        :disabled="!isMyUser"
        @click="addSharingNotes"
        >添加</el-button
      >
    </el-form>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      sharingForm: {
        content: '',
        name: '',
        createdAt: '',
        bus_role: 'hm'
      }
    }
  },
  computed: {
    ...mapGetters(['isMyUser'])
  },
  methods: {
    addSharingNotes() {
      this.$emit('showAddForm')
    },
    sharingFormChange() {
      this.sharingForm.bus_role = ''
      this.$emit('sharingFormChange', this.sharingForm)
    },
    busRoleChange() {
      this.$emit('sharingFormChange', this.sharingForm)
    }
  }
}
</script>
<style lang="scss" scoped>
.el-date-editor .el-range__icon {
  display: none;
}
.el-form-item {
  margin-bottom: 6px;
}
.el-form-item__label {
  line-height: 14px;
}
.sharing_form {
  padding: 0 4px;
  .add_sharing_notes_btn {
    margin-left: 10px;
  }
}
</style>
