<template>
  <span class="SystemContent">
    <p style="margin-bottom:8px;">{{ createdAt }}</p>
    <el-row>
      <p v-show="msgType == 'systemInfo'">
        <span>{{ content }}</span>
      </p>
      <p v-show="msgType == 'systemWarnning'" class="systemWarnning">
        {{ content }}
      </p>
    </el-row>
  </span>
</template>
<script>
export default {
  name: 'SystemContent',
  props: {
    content: {
      type: String,
      default: '超过十分钟'
    },
    createdAt: {
      type: String,
      default: ''
    },
    msgType: {
      type: String,
      default: 'systemInfo' // 定义两种系统消息：一种时系统提示systemInfo，一种是系统警告systemWarnning
    }
  }
}
</script>
<style lang="scss" scoped>
.SystemContent {
  display: inline-block;
  margin-top: 10px;
  text-align: center;
  p {
    line-height: 16px;
    font-size: 12px;
    font-family: PingFangSC-Regular;
    text-align: left;
    color: #576277;
    letter-spacing: 0;
    span {
      line-height: 16px;
    }
  }
  p.systemWarnning {
    padding: 8px 15px;
    color: #576277;
    background: rgba(188, 192, 209, 0.1);
    border-radius: 2px;
  }
}
</style>
