<template>
  <div v-if="serviceInfo">
    <p class="margin-top-20 margin-bottom-6">
      套餐信息
    </p>
    生效中的套餐：{{ fdProduct ? fdProduct.name : '-' }}
    <el-tooltip class="package" effect="dark" placement="top">
      <div slot="content">
        <div class="package-detail">
          <div v-if="department !== 'chanke'">
            绑定医生：{{ serviceInfo.doctor ? serviceInfo.doctor.name : '无' }}
          </div>
          <div v-if="department !== 'chanke'">
            绑定营养师：{{
              serviceInfo.dietician ? serviceInfo.dietician.name : '无'
            }}
          </div>
          <div>
            {{
              department === 'chanke' ? '绑定产科医生：' : '绑定健康管理师：'
            }}
            {{ serviceInfo.hm ? serviceInfo.hm.name : '无' }}
          </div>
          <p class="color-blue" style="margin:10px 0 0;">
            当前套餐中咨询转诊余量：
          </p>
          <div v-if="serviceTransfer.length">
            <div v-for="(item, index) in serviceTransfer" :key="index">
              <p v-if="serverFilter(item).key">
                <span style="display:inline-block;min-width:150px;">
                  {{ serverFilter(item).key }}：
                </span>
                <span> {{ serviceInfo[item] }}</span>
              </p>
            </div>
          </div>
          <div>
            <p style="margin:10px 0 0;">套餐包含其他服务：</p>
            <span
              v-for="(service_items, index) in serviceInfo.service_items"
              :key="'service_items' + index"
            >
              {{ service_items }}</span
            >
          </div>
        </div>
      </div>
      <i class="el-icon-info" />
    </el-tooltip>
    <div>
      服务有效期：
      {{ formatTime(serviceInfo.started_at, '{y}-{m}-{d}') || '无' }}到{{
        formatTime(serviceInfo.ended_at, '{y}-{m}-{d}') || '无'
      }}
    </div>
  </div>
</template>
<script>
export default {
  props: {
    serviceInfo: {
      type: Object,
      default: () => {}
    },
    fdProduct: {
      type: Object,
      default: () => {}
    },
    department: {
      type: String,
      default: 'erke'
    }
  },
  data() {
    return {
      serviceTransferNull: true, //套餐余量是否有
      serviceTransfer: []
    }
  },
  created() {
    this.getServiceByDepartment()
  },
  watch: {
    department(val) {
      this.getServiceByDepartment()
    }
  },
  methods: {
    getServiceByDepartment() {
      if (this.department === 'chanke') {
        this.serviceTransfer = [
          'total',
          'month',
          'common_text_total',
          'common_text_month',
          'common_voice_total',
          'common_voice_month'
        ]
      } else if (this.department === 'erke') {
        this.serviceTransfer = [
          'total',
          'month',
          'doctor_text_total',
          'doctor_text_month',
          'dietician_text_total',
          'dietician_text_month',
          'doctor_voice_month',
          'doctor_voice_total'
        ]
      }
    },
    serverFilter(key) {
      const obj = {
        key: ''
      }
      switch (key) {
        case 'total':
          if (this.department === 'chanke') {
            obj.key = '产科医生咨询总次数'
          } else {
            obj.key = '咨询总次数'
          }
          break
        case 'month':
          if (this.department === 'chanke') {
            obj.key = '本月产科医生咨询次数'
          } else {
            obj.key = '本月咨询次数'
          }
          break
        case 'doctor_text_total':
          obj.key = '图文转诊医生总次数'
          break
        case 'doctor_text_month':
          obj.key = '本月图文转诊医生次数'
          break
        case 'dietician_text_total':
          obj.key = '图文转诊营养师总次数'
          break
        case 'dietician_text_month':
          obj.key = '本月图文转诊营养师次数'
          break
        case 'doctor_voice_month':
          obj.key = '本月电话转诊医生次数'
          break
        case 'doctor_voice_total':
          obj.key = '电话转诊医生总次数'
          break
        // 产科套餐信息
        case 'common_text_total':
          obj.key = '图文转诊其他医生总次数'
          break
        case 'common_text_month':
          obj.key = '本月图文转诊其他医生次数'
          break
        case 'common_voice_total':
          obj.key = '电话转诊其他医生总次数'
          break
        case 'common_voice_month':
          obj.key = '本月电话转诊其他医生次数'
          break
        default:
          return obj
      }
      return obj
    }
  }
}
</script>
