<template>
  <div ref="refmsgCard" class="msg-card">
    <div v-if="msgList && msgList.length > 0" class="infinite-list-wrapper">
      <div>
        <div
          v-for="(item, index) in msgList"
          :key="index"
          style="line-height: 30px;"
        >
          <el-row :gutter="24" class="all">
            <el-col :span="24" :class="searchType + '-right'">
              <p class="color-999 align-left">
                <span>{{ item.account.name }}</span>
                <span class="fontsize-10 margin-left-20">{{
                  new Date(item.created_at) | formatTime()
                }}</span>
              </p>
              <p
                v-if="item.message_body.type === 'text'"
                class="align-left content"
              >
                {{ item.message_body && item.message_body.content }}
              </p>
              <div
                v-if="item.message_body && item.message_body.type === 'link'"
                class="align-left content"
              >
                <p
                  class="link"
                  @click="toURL(item.message_body && item.message_body.content)"
                />
              </div>
              <!--round_msg类型 -->
              <div
                v-if="
                  item.message_body.type === 'robot_card' ||
                    item.message_body.type === 'round_msg'
                "
              >
                <div
                  v-for="(contentItem, ind) in item.message_body.content"
                  :key="contentItem.ask_type ? contentItem.ask_type + ind : ind"
                >
                  <BoutCard
                    :title="item.message_body.title"
                    :reAsk="item.message_body.content[0].ask_type === 're_ask'"
                    :boutCard="
                      boutCardData(
                        contentItem,
                        item.accountType,
                        ind,
                        item.msgOptions
                      )
                    "
                    :msgTime="item.msgTime"
                    @showDetail="showDetail"
                  >
                  </BoutCard>
                  <p
                    v-if="
                      ind < item.message_body.content.length - 1 &&
                        item.message_body.content[0].ask_type !== 're_ask'
                    "
                    :class="
                      item.isSelfSend ? 'chat_divider' : 'chat_divider_left'
                    "
                  ></p>
                </div>
              </div>
              <!-- 电话录音 -->
              <div
                v-if="
                  item.message_body && item.message_body.type === 'voicecard'
                "
                style="padding-bottom: 10px"
              >
                <open-card :title="'点击查看通话录音'">
                  <voice-card
                    :msgtime="item.message_body.msgTime"
                    :src="item.message_body.url"
                    :vduration="item.message_body.duration"
                  />
                </open-card>
              </div>
              <div
                v-if="item.message_body && item.message_body.type === 'image'"
                class="image-box"
                @click="thumbClickFun('image', item.message_body.url)"
              >
                <img :src="item.message_body && item.message_body.url" alt="" />
              </div>
              <!-- 语音消息 -->
              <div
                v-if="item.message_body && item.message_body.type === 'voice'"
                class="image-box"
              >
                <open-card :title="'点击查看语音'">
                  <audio :src="item.message_body.url" controls />
                </open-card>
              </div>
              <!-- 视频消息 -->
              <div
                v-if="item.message_body && item.message_body.type === 'video'"
                class="image-box"
              >
                <div
                  class="VideoContent"
                  @click="thumbClickFun('video', item.message_body.url)"
                >
                  <img
                    :src="
                      item.message_body &&
                        item.message_body.url +
                          '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    :data-src="
                      item.message_body &&
                        item.message_body.url +
                          '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    alt=""
                  />
                  <i class="el-icon-video-play" />
                </div>
              </div>
              <div
                v-if="item.message_body && item.message_body.type === 'notice'"
                class="align-left"
              >
                <SystemContent
                  style="font-size: 12px;text-align:left;"
                  :content="item.message_body && item.message_body.content"
                />
              </div>
              <div
                v-if="item.message_body && item.message_body.type === 'card'"
              >
                <open-card :title="'点击查看卡片'">
                  <CardContent
                    :images-options="item.message_body"
                    :baby="item.message_body.content.family_member"
                    :symptom="item.message_body.content.ask_question_desc"
                    :has-interven="item.message_body.content.has_interven"
                    :interven-desc="item.message_body.content.interven_desc"
                    :data-arr="item.message_body.content.ask_question_report"
                    @showDetail="showDetail"
                  />
                </open-card>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div v-else style="text-align: center;color: #5a5e66;">
      咨询记录
    </div>
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUploadUrlList"
    />
  </div>
</template>
<script>
import { boutCardData } from '@/filters'
import OpenCard from '@/views/evaluate/MedicalService/components/OpenCard'
import SystemContent from './SystemContent'
import BoutCard from '@/views/medical-service/diagnose/chat/chatArea/components/BoutCard'
import Monitor from '@/components/Monitor'
import CardContent from '@/views/medical-service/diagnose/chat/chatArea/components/CardContent'
import VoiceCard from '@/views/medical-service/diagnose/chat/chatArea/components/voiceCard'

export default {
  components: {
    SystemContent,
    CardContent,
    Monitor,
    VoiceCard,
    OpenCard,
    BoutCard
  },
  props: {
    searchType: {
      type: String,
      default: 'all'
    },
    msgList: {
      type: Array,
      default: () => []
    }
  },
  watch: {
    msgList() {
      console.log('msgList==', this.msgList)
    }
  },
  data() {
    return {
      monitorInfo: {},
      curUploadUrlList: [],
      imageArr: []
    }
  },
  created() {},
  methods: {
    boutCardData,
    showDetail(val) {
      this.thumbClickFun(val.mediaType, val.meidiaUrl)
    },
    thumbClickFun(type, url) {
      this.monitorInfo = {
        type,
        meta: { url }
      }
      this.curUploadUrlList.push(url)
      this.$refs.monitor.show()
    },
    toURL(val) {
      if (val) {
        window.open(val)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.cardVideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    bottom: 40%;
    left: 31%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
.VideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    top: 30%;
    left: 30%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
.msg-card {
  overflow: auto;
  padding-left: 30px;
  max-height: 544px;
  // padding-bottom: 50px;
  font-family: PingFangSC-Regular;
  .el-row.all {
    padding: 10px 15px 0;
    &:first-child {
      border-top: 1px solid #e8eaec !important;
    }
    &:last-child {
      border-top: 1px solid #e8eaec !important;
    }
    &:nth-child(odd) {
      background: #fff;
      box-shadow: inset 0 1px 0 0 #e8eaec;
    }
    &:nth-child(even) {
      background: #fff;
      box-shadow: inset 0 1px 0 0 #e8eaec;
    }
    &:hover {
      background: #f6f7fb;
    }
  }
  .content {
    padding-top: 10px;
    line-height: 28px;
    font-size: 12px;
    color: #576277;
  }
  .image-box {
    position: relative;
    float: left;
    // display:inline-block;
    text-align: left;
    img {
      width: 100px;
      height: 100px;
    }
  }
}
</style>
