import request from '@/utils/request'

export function getNotesList(babyId, params) {
  delete params.created_at
  delete params.count
  return request({
    url: `/shared_remarks/list/${babyId}`,
    method: 'get',
    params: params
  })
}
export function addNotes(babyId, content) {
  return request({
    url: `/shared_remarks/create/${babyId}`,
    method: 'post',
    data: content
  })
}
export function editNotes(notesId, content) {
  return request({
    url: `/shared_remarks/edit/${notesId}`,
    method: 'put',
    data: content
  })
}
export function delNotesApi(notesId) {
  return request({
    url: `/shared_remarks/destroy/${notesId}`,
    method: 'DELETE'
  })
}
