<template>
  <div class="comment">
    <div class="success-item">
      <div class="label">
        <div class="avatar">
          <el-avatar :size="'small'" :src="taskDetail.user.avatar" />
        </div>
        用户:
      </div>
      <div class="content">
        {{
          taskDetail.user.remark_name
            ? taskDetail.user.remark_name
            : taskDetail.user.nickname
        }}
        &nbsp;&nbsp;
        <svg-icon v-if="taskDetail.is_replace" icon-class="replace" />
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-tooltip effect="dark" :content="likeTooltip" placement="top">
          <span :class="['like', taskDetail.like ? '' : 'unlike']"
            ><i class="iconfont icon-dianzan"
          /></span>
        </el-tooltip>
      </div>
      <div v-if="taskDetail.delayDay && taskDetail.delayDay > 0" class="delay">
        该任务已延期{{ taskDetail.delayDay }}天
      </div>
    </div>
    <div v-if="taskDetail.subType === 5">
      <div class="success-item">
        <div class="label">参考知识点:</div>
        <div class="content user-replay">
          {{ taskDetail.task_science_schedule.desc }}
        </div>
      </div>
      <el-row style="margin-left: 150px" class="margin-top-20">
        <el-col
          v-for="(image, index) in taskDetail.task_science_schedule.icons"
          :key="index"
          :span="2"
        >
          <img
            :src="image.url"
            width="50"
            height="50"
            @click="showImage(image, taskDetail.task_science_schedule.icons)"
          />
        </el-col>
      </el-row>
      <div class="tips">
        <div class="tips-title">
          温馨提示：
        </div>
        <div class="tips-content">
          以上知识点为教研团队提供的参考内容，用户不可见。
        </div>
      </div>
    </div>
    <div v-if="taskDetail.subType === 6">
      <div class="success-item">
        <div class="label">本次接种:</div>
        <div class="content user-replay">
          <vaccine-table
            :table-data="taskDetail.task_vaccine_schedule.curTask"
          ></vaccine-table>
        </div>
      </div>
      <div
        v-for="(item, index) in taskDetail.task_vaccine_schedule.curTask"
        :key="'cur' + index"
      >
        <div class="success-item">
          <div class="label"><span v-if="index === 0">参考知识点:</span></div>
          <div class="content user-replay">
            {{ item.desc }}
          </div>
        </div>
        <el-row style="margin-left: 150px" class="margin-top-20">
          <el-col
            v-for="(image, index) in item.icons"
            :key="'curIcon' + index"
            :span="2"
          >
            <img
              :src="image.url"
              width="50"
              height="50"
              @click="showImage(image, item.icons)"
            />
          </el-col>
        </el-row>
      </div>
      <div class="success-item">
        <div class="label">下次接种</div>
        <div class="content user-replay">
          <vaccine-table
            :table-data="taskDetail.task_vaccine_schedule.nextTask"
          ></vaccine-table>
        </div>
      </div>
      <div
        v-for="(item, index) in taskDetail.task_vaccine_schedule.nextTask"
        :key="'next' + index"
      >
        <div class="success-item">
          <div class="label"><span v-if="index === 0">参考知识点:</span></div>
          <div class="content user-replay">
            {{ item.desc }}
          </div>
        </div>
        <el-row style="margin-left: 150px" class="margin-top-20">
          <el-col
            v-for="(image, index) in item.icons"
            :key="'nextIcon' + index"
            :span="2"
          >
            <img
              :src="image.url"
              width="50"
              height="50"
              @click="showImage(image, item.icons)"
            />
          </el-col>
        </el-row>
      </div>
      <div class="tips">
        <div class="tips-title">
          温馨提示：
        </div>
        <div class="tips-content">
          以上知识点为教研团队提供的参考内容，用户不可见。
        </div>
      </div>
    </div>
    <div class="success-item">
      <div class="label">标题:</div>
      <div class="content">{{ taskDetail.title }}</div>
    </div>
    <div class="success-item" v-if="taskDetail.subType === 4">
      <div class="label">备注:</div>
      <div class="content">{{ taskDetail.desc ? taskDetail.desc : '无' }}</div>
    </div>
    <div class="success-item">
      <div class="label">创建时间:</div>
      <div class="content">
        {{ new Date(taskDetail.createTime) | formatTime() }}
      </div>
    </div>
    <div class="success-item">
      <div class="label">计划执行日期:</div>
      <div class="content">
        {{ new Date(taskDetail.executeTime) | formatTime('y-m-d') }}
      </div>
    </div>
    <div class="success-item">
      <div class="label">内容详情:</div>
      <div
        class="content rich-text rich-text__image--max480"
        v-html="taskDetail.content"
      />
    </div>
    <div
      v-if="taskDetail.contentVideos && taskDetail.contentVideos.length > 0"
      class="success-item"
    >
      <div class="label"></div>
      <div class="content">
        <div
          v-for="{ url } in taskDetail.contentVideos"
          :key="url"
          style="margin-right: 20px;"
        >
          <video-item :url="url" />
        </div>
      </div>
    </div>
    <div v-if="taskDetail.sendTime" class="time-stamp">
      {{ new Date(taskDetail.sendTime) | formatTime() }}
    </div>
    <div v-if="taskDetail.delayReason" class="success-item">
      <div class="label">延期原因:</div>
      <div class="content">{{ taskDetail.delayReason }}</div>
    </div>
    <div v-if="taskDetail.cancelTime" class="success-item">
      <div class="label">取消时间:</div>
      <div class="content">
        {{ new Date(taskDetail.cancelTime) | formatTime() }}
      </div>
    </div>
    <div v-if="taskDetail.comment && taskDetail.comment.ask">
      <div class="success-item">
        <div class="label">{{ taskDetail.comment.name }}留言:</div>
        <div class="content user-replay">{{ taskDetail.comment.ask }}</div>
      </div>
      <div v-if="taskDetail.comment" class="time-stamp">
        {{ new Date(taskDetail.comment.askTime) | formatTime() }}
      </div>
    </div>
    <div
      v-if="taskDetail.comment && taskDetail.comment.answerContent"
      class="success-item"
    >
      <div class="reply-label">
        健康管理师回复:
        <div style="text-align: right">
          （{{ taskDetail.comment.replyName }}）
        </div>
      </div>
      <div class="content user-replay health-replay">
        {{ taskDetail.comment.answerContent }}
      </div>
    </div>
    <div
      v-if="taskDetail.comment && taskDetail.comment.answerTime"
      class="time-stamp"
    >
      {{ new Date(taskDetail.comment.answerTime) | formatTime() }}
    </div>
    <!--图片显示器-->
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUrlList"
    />
  </div>
</template>

<script>
import { checkDesc } from '@/api/healthManagement'
import VideoItem from './VideoItem'
import VaccineTable from '../VaccineTable'
import Monitor from '@/components/Monitor'

export default {
  name: 'TaskDetail',
  components: {
    VideoItem,
    VaccineTable,
    Monitor
  },
  data() {
    return {
      curUrlList: [],
      monitorInfo: {},
      currentTask: {},
      taskDetail: {
        user: '',
        title: '',
        content: ''
      }
    }
  },
  mounted() {
    checkDesc(this.$route.query.taskId).then(res => {
      if (res.code === 20000) {
        this.taskDetail = res.data
      }
    })
  },
  computed: {
    likeTooltip() {
      return this.taskDetail.like ? '用户赞了你' : '还未得到用户的赞'
    }
  },
  methods: {
    // 查看图片详情
    showImage(image, urlList) {
      this.curUrlList = urlList.map(({ url }) => url)
      this.monitorInfo = {
        type: image.type,
        meta: { url: image.url }
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
.tips {
  display: flex;
  align-items: flex-start;
  margin-top: 10px;
  margin-left: 95px;
  color: #f8747f;
  margin-bottom: -20px;

  .tips-title {
    width: 120px;
    font-weight: bold;
    text-align: right;
  }

  .tips-content {
    padding-left: 10px;
  }
}
@mixin reference {
  margin-bottom: -50px;
  margin-top: 20px;
  padding: 30px;
}
.guidance-reference {
  @include reference;
}
.vaccine-reference {
  @include reference;
}
.health-replay {
  padding-left: 20px !important;
}
.reply-label {
  margin-right: 10px;
  font-weight: bold;
}
.comment {
  padding-bottom: 20px;
}
.rich-text {
  margin-top: -5px;
  line-height: 24px;
  word-wrap: break-word;
}
.delay {
  position: absolute;
  top: -10px;
  right: 600px;
  padding: 5px 10px;
  font-size: 14px;
  color: #f8747f;
  background-color: rgba(248, 116, 127, 0.1);
  border: 1px solid #f8747f;
  border-radius: 5px;
}
.like {
  color: #ffb718;
}
.unlike {
  color: #999;
}
.time-stamp {
  padding-left: 145px !important;
}
</style>
