<template>
  <div class="extend_basic_info">
    <userInfo
      :pageType="'health'"
      :user="userInfo"
      @updateInfo="getBasicInfoById"
    ></userInfo>
    <p class="margin-top-20 margin-bottom-6" style="color: #576277;">
      {{ title }}信息
      <svg-icon v-if="data.family_member.is_replace" icon-class="replace">
      </svg-icon>
    </p>
    <babyInfo
      :isShow="true"
      :babyDetail="data"
      :department="department"
    ></babyInfo>
  </div>
</template>
<script>
import userInfo from '../chat/userInfo/components/user'
import babyInfo from '../chat/userInfo/components/baby'
import { getBasicInfo } from '@/api/userInfo'
export default {
  name: 'basicInfo',
  components: {
    userInfo,
    babyInfo
  },
  props: {
    babyId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      userInfo: {},
      healthRecord: {},
      contractInfo: {},
      data: {
        family_member: {}
      },
      department: '',
      title: ''
    }
  },
  watch: {
    babyId() {
      this.getBasicInfoById()
    }
  },
  created() {
    this.getBasicInfoById()
  },
  methods: {
    getBasicInfoById() {
      if (!this.babyId) {
        return
      }
      getBasicInfo(this.babyId).then(res => {
        if (res && res.data) {
          this.userInfo = res.data.user_info
          this.userInfo.is_replace = res.data.family_member_info.is_replace
          this.contractInfo = res.data.contract_info
          this.healthRecord = res.data.health_record
          this.department = res.data.family_member_info.type
          res.data.family_member = res.data.family_member_info
          res.data.chanke = {
            cur_stage: res.data.family_member_info.cur_stage
          }
          res.data.remark = res.data.family_member_info.remark
          res.data._id = res.data.family_member_info._id

          this.title = this.department === 'chanke' ? '产科患者' : '儿科'
          if (this.department === 'erke') {
            res.data.health_record = {
              baby_detail_info: {
                wyfs: ''
              }
            }
          }
          if (!res.data.family_member_info.family_member) {
            res.data.family_member_info.family_member =
              res.data.family_member_info
          }
          res.data.fd_contract_info = res.data.contract_info
          res.data.fd_product = res.data.contract_info.fd_product_info
          this.data = res.data
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.service_info {
  line-height: 20px;
  font-size: 14px;
  color: #576277;
}
</style>
