<template>
  <div class="drawer">
    <div :class="maskClass" @click="closeByMask" />
    <div :class="mainClass" :style="mainStyle" class="main">
      <div class="drawer-head">
        <span v-show="!closable" class="close-btn-right" @click="closeByButton">
          <i class="el-icon-arrow-left" />
        </span>
        <span
          >{{ title }}
          <span v-if="title" style="color: #2d8cf0">
            {{
              title === '咨询中的用户'
                ? askingCustomerList.length
                : title === '转诊中的用户'
                ? transferCustomerList.length
                : waitingCustomerList.length
            }}
          </span>
        </span>
        <span v-show="closable" class="close-btn" @click="closeByButton"
          ><i class="el-icon-close"
        /></span>
      </div>
      <div class="drawer-body">
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  props: {
    direction: {
      type: String,
      default: 'right'
    },
    // 是否打开
    display: {
      type: Boolean
    },

    // 标题
    title: {
      type: String,
      default: '标题'
    },

    // 是否显示关闭按钮
    closable: {
      type: Boolean,
      default: true
    },

    // 是否显示遮罩
    mask: {
      type: Boolean,
      default: true
    },

    // 是否点击遮罩关闭
    maskClosable: {
      type: Boolean,
      default: true
    },

    // 宽度
    width: {
      type: String,
      default: '400px'
    },

    // 是否在父级元素中打开
    inner: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    ...mapGetters([
      'waitingCustomerList',
      'askingCustomerList',
      'transferCustomerList'
    ]),
    maskClass: function() {
      return {
        'mask-show': this.mask && this.display,
        'mask-hide': !(this.mask && this.display),
        inner: this.inner
      }
    },
    mainClass: function() {
      return {
        'main-show': this.display,
        'main-hide': !this.display,
        inner: this.inner
      }
    },
    mainStyle: function() {
      return {
        width: this.width,
        right: this.display
          ? '0'
          : `${this.direction === 'right' ? '+' : '-'}${this.width}`
      }
    }
  },
  mounted() {
    if (this.inner) {
      const box = this.$el.parentNode
      box.style.position = 'relative'
    }
  },
  methods: {
    closeByMask() {
      this.maskClosable && this.$emit('update:display', false)
    },
    closeByButton() {
      this.$emit('update:display', false)
      this.$emit('closeDrawer')
    }
  }
}
</script>

<style lang="scss" scoped>
.drawer {
  /* 遮罩 */
  .mask-show {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 1;
    z-index: 10;
    transition: opacity 0.5s;
  }
  .mask-hide {
    opacity: 0;
    transition: opacity 0.5s;
  }

  /* 滑块 */
  .main {
    position: fixed;
    top: 0;
    height: 830px;
    background: #fff;
    z-index: 10;
    transition: all 0.5s;
  }
  .main-show {
    opacity: 1;
  }
  .main-hide {
    opacity: 0;
  }

  /* 某个元素内部显示 */
  .inner {
    position: absolute;
  }

  /* 其他样式 */
  .drawer-head {
    display: flex;
    justify-content: space-between;
    margin-top: -10px;
    padding: 15px 15px 15px 25px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    color: #17233d;
    margin-bottom: 20px;
    .close-btn {
      display: inline-block;
      padding-left: 20px;
      height: 100%;
      cursor: pointer;
    }
    .close-btn-right {
      height: 100%;
      font-size: 16px;
      cursor: pointer;
    }
  }
  .drawer-body {
    font-size: 14px;
  }
}
</style>
