<template>
  <div>
    <!--抽屉里的咨询详情 -->
    <drawer
      title=""
      :display.sync="display"
      :inner="true"
      :width="drawerWidth"
      :mask="false"
      :direction="'left'"
      :closable="false"
    >
      <scroll-box :box-height="670">
        <range-history
          :range-history="relatedHistory"
          :department="department"
          @openMessageList="openMessageList"
        />
      </scroll-box>
    </drawer>
    <!--聊天记录抽屉 -->
    <chat-history
      title=""
      :display.sync="displayChat"
      :inner="true"
      :width="drawerWidth"
      :mask="false"
      :direction="'left'"
    >
      <div>
        <msg-card :msg-list="messageList" />
      </div>
    </chat-history>
    <!--所有咨询历史-->
    <div class="history-search-container">
      <div class="history-search-item">
        <div class="history-search-label">建议叮嘱搜索</div>
        <el-input
          v-model="searchText"
          class="history-search-value"
          clearable
          size="mini"
          placeholder="请输入搜索内容"
          prefix-icon="el-icon-search"
          @input="search"
        />
      </div>
      <div class="history-search-label">咨询开始时间</div>
      <div class="history-search-item">
        <el-date-picker
          v-model="searchDateRange"
          class="history-search-value"
          format=" yyyy/MM/dd hh:mm:ss"
          :picker-options="pickerOptions"
          size="mini"
          type="datetimerange"
          range-separator="-"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          @change="search"
        />
      </div>
    </div>
    <div v-if="!searchText && !searchDateRange" class="search-result">
      请输入搜索内容
    </div>
    <div v-else-if="searchText && allHistory.length > 0" class="search-result">
      {{ historyTotalCount }}条与<span style="color: red">{{ searchText }}</span
      >相关内容
    </div>
    <div
      v-else-if="allHistory.length === 0 && resultPanelShow"
      class="search-result"
    >
      没有找到相关记录
    </div>
    <div class="all-history-card">
      <scroll-box :box-height="extendAllHistoryHeight">
        <div v-infinite-scroll="loadMoreData" infinite-scroll-immediate="false">
          <div v-for="(talk, index) in allHistory" :key="index">
            <div class="talk-title">
              <div class="text">
                <div>
                  {{ new Date(talk.created_at) | formatTime() }}
                </div>
                <div
                  v-if="talk.question_type == 'disease'"
                  class="align-right margin-right-10"
                >
                  <span style="margin-right:10px;">疾病护理类</span>
                  {{ talk.ask_type }}
                </div>
                <div v-else class="align-right margin-right-10">
                  {{ department === 'chanke' ? '其他问题' : '其他育儿类' }}
                </div>
              </div>
              <div class="icon" @click="showDrawerHistory(talk)">
                <i class="el-icon-arrow-right" />
              </div>
            </div>
            <div class="talk-content">
              <div v-if="talk.hm">
                {{ systemRole('hm', '', department) }}：{{ talk.hm.name || '' }}
              </div>
              <div v-if="talk.dietician">
                营养师：{{ talk.dietician.name || '' }}
              </div>
              <div v-if="talk.doctor">医生：{{ talk.doctor.name || '' }}</div>
              <div>状态： {{ resolveStatus(talk.status, '') }}</div>
              <div>
                {{
                  talk.suggestion_type
                    ? suggestType(talk.suggestion_type)
                    : '叮嘱'
                }}
                ：
                <span
                  v-html="
                    searchText
                      ? searchHighlight(talk.suggestion, searchText)
                      : talk.suggestion
                  "
                />
              </div>
              <el-button
                type="text"
                class="review-chat"
                @click="showDrawerHistory(talk)"
              >
                查看该段咨询
              </el-button>
            </div>
          </div>
        </div>
      </scroll-box>
    </div>
  </div>
</template>
<script>
import ChatHistory from './ChatHistory'
import MsgCard from './MsgCard'
import Drawer from '../../components/common/Drawer'
import RangeHistory from './RangeHistory'
import { getAllHistory, getRecentHistory } from '@/api/chat'
import { datePickerOptions } from '@/utils/chat'
import { systemRole, resolveStatus, suggestType } from '@/utils/dictionary'
import { fdAskLocation } from '@/api/message'

import ScrollBox from '@/components/ScrollBox'
import _ from 'lodash'

export default {
  name: 'CaseHistoryAll',
  components: {
    Drawer,
    RangeHistory,
    ChatHistory,
    ScrollBox,
    MsgCard
  },
  props: {
    babyId: {
      type: String,
      default: ''
    },
    questionType: {
      type: String,
      default: ''
    },
    sessionLastAsk: {
      type: String,
      default: ''
    },
    department: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      displayChat: false,
      messageList: [],
      pickerOptions: datePickerOptions(),
      relatedHistory: [],
      historyTotalCount: null,
      allHistory: [],
      currentDetail: [],
      pageSize: 10,
      resultPanelShow: false,
      searchDateRange: null,
      searchText: '',
      display: false,
      drawerWidth: '100%'
    }
  },
  computed: {
    extendAllHistoryHeight() {
      if (this.$store.state.babyExtend.pageType === 'chat') {
        return 570
      } else {
        return 550
      }
    }
  },
  watch: {
    questionType() {
      this.resetAllHistory()
      this.getHistory()
    },
    sessionLastAsk(val) {
      this.resetAllHistory()
      this.getHistory()
    }
  },
  created() {
    this.resetAllHistory()
    this.getHistory()
  },
  methods: {
    systemRole,
    resolveStatus,
    suggestType,
    openMessageList(fdAsk) {
      this.messageList = []
      this.displayChat = true
      fdAskLocation(fdAsk).then(res => {
        this.messageList = res.data.list
      })
    },
    resetAllHistory() {
      this.searchText = ''
      this.searchDateRange = null
      this.allHistory = []
      this.displayChat = false
      this.resultPanelShow = false
      this.display = false
    },
    search() {
      this.resultPanelShow = false
      // 搜索条件为空时，清空咨询历史，不向后台发送请求
      // if (!this.searchText && this.searchDateRange === null) {
      //   this.allHistory = []
      //   return
      // }
      this.getRemoteData()
    },
    // 打开抽屉
    showDrawerHistory(talk) {
      const params = {
        last_three: 0,
        fd_ask: talk._id
      }
      getRecentHistory(params).then(res => {
        if (res.code === 20000) {
          const relatedHistory = res.data.map(history => {
            // 为咨询历史添加必要字段，待优化
            history.list = history.list.map(talk => {
              return {
                ...talk,
                open: false
              }
            })
            return history
          })
          this.relatedHistory = relatedHistory
          this.display = true
        }
      })
    },
    // 搜素高亮
    searchHighlight(sentence, highlightWord) {
      if (!sentence) {
        return
      }
      const replaceReg = new RegExp(highlightWord, 'g')
      const replaceString =
        '<span style="color: red">' + highlightWord + '</span>'
      const result = sentence.replace(replaceReg, replaceString)
      return result
    },
    getHistory() {
      const currentFdask = this.sessionLastAsk || ''
      if (!currentFdask) {
        this.allHistory = []
      } else {
        const type = this.questionType == 'all' ? '' : this.questionType
        const params = {
          question_type: type,
          fd_ask: currentFdask,
          search_key: this.searchText,
          page_size: this.pageSize,
          current_page: 1,
          start_at: this.searchDateRange ? this.searchDateRange[0] : '',
          end_at: this.searchDateRange ? this.searchDateRange[1] : ''
        }
        getAllHistory(params).then(res => {
          if (res.code === 20000) {
            this.allHistory = res.data.list
            this.historyTotalCount = res.data.count
            this.resultPanelShow = true
          }
        })
      }
    },
    // 防抖动的方式从远端获取数据
    getRemoteData: _.debounce(function() {
      this.historyTotalCount = 0
      this.pageSize = 10
      this.getHistory()
    }, 300),
    loadMoreData() {
      // 滚动条触底时加载更多数据
      this.pageSize += 1
      if (this.pageSize <= this.historyTotalCount) {
        this.getHistory()
      }
    }
  }
}
</script>
<style lang="scss">
.history-search-item {
  .el-input--mini .el-input__inner {
    margin-left: -5px;
  }
}
</style>

<style scoped lang="scss">
.talk-title {
  display: flex;
  justify-content: space-between;
  padding: 10px 0 10px 10px;
  width: 100%;
  .text {
    display: flex;
    justify-content: space-around;
    font-size: 14px;
    color: #17233d;
    flex: 7;
    border-left: 3px solid #0085ff;
    div {
      margin-left: 10px;
      flex: 1;
      &:first-child {
        flex: 1;
      }
    }
  }
  .icon {
    flex: 1;
    cursor: pointer;
  }
}
.talk-content {
  position: relative;
  padding: 0 10px;
  line-height: 30px;
  font-size: 14px;
  color: #17233d;
  .review-chat {
    bottom: 12px;
    float: right;
    margin-right: 16px;
    &::after {
      display: block;
      clear: both;
      content: '';
    }
  }
}
.history-search-container {
  display: flex;
  justify-content: space-around;
  padding-left: 10px;
  height: 100px;
  color: #17233d;
  flex-direction: column;
  .history-search-item {
    display: flex;
    align-items: center;
    flex-direction: row;
    .history-search-label {
      flex: 2;
    }
    .history-search-value {
      flex: 5;
    }
  }
}
.search-result {
  margin-top: 20px;
  height: 30px;
  font-size: 14px;
  text-align: center;
  color: #17233d;
  border-bottom: 1px solid #e8eaec;
}
.all-history-card {
  margin-top: 20px;
  .open-detail-button {
    text-align: right;
    cursor: pointer;
  }
}
</style>
