<template>
  <div>
    <img
      v-if="mediaType === 'image'"
      :src="url"
      alt=""
      width="50px"
      height="50px"
      @click="showMediaDetail(urlList, mediaType, url)"
    />
    <div v-else-if="mediaType === 'video'" class="video-container">
      <video :src="url" controls="controls" height="50" width="50" />
      <div class="video-mask" @click="showMediaDetail(urlList, mediaType, url)">
        <span class="el-icon-video-play" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MediaDisplayItem',
  props: {
    url: {
      type: String,
      default: ''
    },
    mediaType: {
      type: String,
      default: ''
    },
    showMediaDetail: {
      type: Function,
      default: () => {}
    },
    urlList: {
      type: Array,
      default: () => []
    }
  }
}
</script>

<style scoped lang="scss">
.video-container {
  position: relative;
  .video-mask {
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: transparent;
    cursor: pointer;
    span {
      font-size: 20px;
      color: #fff;
      &:hover {
        color: #2d8cf0;
      }
    }
  }
}
</style>
