<template>
  <div v-if="isShow" class="baby-info-card">
    <!-- 产科或儿科信息 -->
    <div v-if="userInfoItem.length">
      <div v-for="(item, index) in userInfoItem" :key="index">
        <span v-if="item.show">
          <span v-if="item.label">{{ item.label }}</span>
          <span class="span-val" v-if="item.value">
            {{ item.value }}
          </span>
          <p class="span-val" v-if="item.html" v-html="item.html"></p>
        </span>
      </div>
    </div>
    <div class="margin-top-10">
      <span
        class="add_remark_btn"
        :class="role !== 'hm' ? 'notAllow' : ''"
        @click="showAddTextareaFun"
        >{{ tipContent }}备注（内部可见、不限字数）</span
      >
    </div>
    <!-- 有备注 -->
    <div v-if="haveRemark && !showAddTextarea">
      <p v-if="remark" class="remark_text">{{ remark }}</p>
      <p v-if="remarkCreatedAt" class="created_at">
        {{ formatTime(remarkCreatedAt) }}
      </p>
    </div>
    <div v-if="showAddTextarea" class="clearfix">
      <el-input
        v-model="remark"
        type="textarea"
        rows="4"
        aria-placeholder="请填写备注信息"
      />
      <el-button
        type="primary"
        class="float-right margin-top-10"
        size="mini"
        @click="saveRemarkFun"
        >保存</el-button
      >
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { addBabyRemark } from '@/api/userInfo'
import { getChankeInfo } from '@/utils/chat'
import {
  curStageTxt,
  booleanToTxt,
  measureToTxt,
  feedModeTxt,
  fenmianModeTxt
} from '@/filters'
export default {
  name: 'BabyInfoCard',
  props: {
    babyDetail: {
      type: Object,
      deault: () => {}
    },
    isShow: {
      type: Boolean,
      default: false
    },
    department: {
      type: String,
      default: 'erke'
    }
  },
  data() {
    return {
      userInfoItem: [],
      showAddTextarea: false,
      tipContent: '',
      remark: '', // 备注内容
      remarkCreatedAt: '',
      haveRemark: '' //是否已有备注
    }
  },
  computed: {
    ...mapGetters({
      role: 'role'
    })
  },

  watch: {
    babyDetail: {
      handler() {
        this.init()
      },
      deep: true
    },
    isShow() {
      this.init()
    }
  },
  created() {
    this.init()
  },
  methods: {
    fenmianModeTxt,
    feedModeTxt,
    getChankeInfo,
    init() {
      if (this.isShow) {
        this.initCard()
        if (this.department === 'chanke') {
          this.getChankeData(this.babyDetail)
        } else {
          this.getErkeData(this.babyDetail)
        }
      }
    },
    curStageTxt,
    booleanToTxt,
    measureToTxt,
    getErkeData(data) {
      this.userInfoItem = []
      let wyfs = ''
      let basicInfo = data.family_member
      if (basicInfo.health_record && basicInfo.health_record.baby_detail_info) {
        let wyfsData = basicInfo.health_record.baby_detail_info.wyfs
        if (wyfsData && wyfsData.length) {
          wyfs = wyfsData.join('，')
        }
      } else {
        wyfs = '未知'
      }
      let arr = [
        {
          show: true,
          label: '性别：',
          value:
            basicInfo.gender === 1
              ? '男'
              : basicInfo.gender === 2
              ? '女'
              : '未知'
        },
        {
          show: true,
          label: '出生孕周：',
          value:
            basicInfo.gestational_weeks || basicInfo.gestational_weeks === 0
              ? basicInfo.gestational_weeks +
                '周' +
                basicInfo.gestational_days +
                '天'
              : ''
        },
        {
          show: true,
          label: '出生日期：',
          value: this.formatTime(basicInfo.birthday, '{y}-{m}-{d}')
        },
        {
          show: true,
          label: '月龄：',
          value: basicInfo.age_of_month
        },
        {
          show: true,
          label: '喂养方式：',
          value: wyfs
        }
      ]
      this.userInfoItem = arr
    },
    getChankeData(info) {
      this.userInfoItem = []
      const data = info.family_member
      const curStage = data.chanke ? data.chanke.cur_stage : data.cur_stage
      const healthRecord = info.health_record
        ? info.health_record
        : data.health_record
      if (healthRecord) {
        const basicInfo = Object.assign(
          {},
          healthRecord.chanke_basic_info.yuejing,
          healthRecord.chanke_basic_info.chanke_history,
          healthRecord.chanke_byz,
          healthRecord.chanke_hyz,
          healthRecord.chanke_crq
        )
        this.userInfoItem = this.getChankeInfo(
          curStage,
          basicInfo,
          '',
          '',
          true
        )
      }
    },
    initCard() {
      const babyDetail = this.babyDetail.family_member
      this.showAddTextarea = false
      this.haveRemark =
        babyDetail && babyDetail.remark ? babyDetail.remark.content : ''
      this.remark =
        babyDetail && babyDetail.remark ? babyDetail.remark.content : ''
      if (this.remark) {
        this.remarkCreatedAt = babyDetail.remark.created_at
      }
      this.tipContent = this.haveRemark ? '修改' : '添加'
    },
    showAddTextareaFun() {
      if (this.role === 'hm') {
        this.showAddTextarea = true
      }
    },
    hideAddTextareaFun() {
      this.showAddTextarea = false
    },
    saveRemarkFun() {
      if (!this.remark) {
        this.$message({ type: 'warning', message: '备注不能为空' })
        return
      }
      const babyId = this.babyDetail.family_member._id
      addBabyRemark(babyId, { remark: this.remark }).then(res => {
        if (res && res.data.result) {
          this.hideAddTextareaFun()
          this.haveRemark = true
          this.remark = res.data.remark.content
          this.remarkCreatedAt = res.data.remark.created_at
          this.tipContent = '修改'
          if (this.$route.path.indexOf('diagnose') !== -1) {
            // 刷新聊天窗口顶部客户信息
            this.$store.dispatch('chat/getActivedUserInfo')
          }
        }
        const operationName = '保存'
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result
            ? operationName + '成功'
            : operationName + '失败'
        })
      })
    }
  }
}
</script>
