<template>
  <div class="CardContent">
    <div>
      <el-row class="baby-info" :gutter="4">
        <el-col :span="2">
          <svg-icon icon-class="baby" />
        </el-col>
        <el-col :span="22">
          <p>宝宝信息</p>
          <div>
            姓名：{{ baby.name }} &nbsp;&nbsp; 性别：{{
              genderFilter(baby.gender)
            }}
            &nbsp;&nbsp; 年龄：{{
              baby.age_of_month ? baby.age_of_month : '未知'
            }}
          </div>
        </el-col>
      </el-row>
      <el-row class="symptom" :gutter="24">
        <el-col :span="2">
          <svg-icon icon-class="yiliaoqixie" />
        </el-col>
        <el-col :span="22">
          <p>症状描述</p>
          <div>{{ symptom }}</div>
        </el-col>
      </el-row>
      <el-row class="interven" :gutter="24">
        <el-col :span="2">
          <svg-icon icon-class="yao" />
        </el-col>
        <el-col :span="22">
          <p>干预史</p>
          <div>{{ intervenDesc || '无' }}</div>
        </el-col>
      </el-row>
      <el-row class="data" :gutter="24">
        <el-col :span="2">
          <svg-icon icon-class="tupian" />
        </el-col>
        <el-col :span="22">
          <p>资料照片及视频</p>
          <div v-if="dataArr && dataArr.length > 0">
            <div
              v-for="(item, subIndex) in dataArr"
              :key="subIndex"
              class="image-and-video"
            >
              <img
                v-if="item.type === 'image'"
                :src="item.url"
                alt=""
                width="50px"
                @click="showMediaDetail(item.url, item.type)"
              />
              <div v-else-if="item.type === 'video'" class="video-container">
                <!-- 视频消息 -->
                <div class="VideoContent">
                  <img
                    :src="
                      item.url +
                        '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    :data-src="
                      item.url +
                        '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    alt=""
                  />
                  <i
                    class="el-icon-video-play"
                    @click="showMediaDetail(item.url, 'video')"
                  />
                </div>
              </div>
            </div>
          </div>
          <p v-if="dataArr.length <= 0">无</p>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
const genderFilter = Vue.filter('gender')
export default {
  name: 'CardContent',
  components: {},
  props: {
    baby: {
      type: Object,
      default: () => ({
        name: '未知',
        gender: 0,
        age: '未知'
      })
    },
    symptom: {
      type: String,
      default: ''
    },
    hasInterven: {
      type: Boolean,
      default: false
    },
    intervenDesc: {
      type: String,
      default: ''
    },
    dataArr: {
      type: Array,
      default: () => ['http://']
    }
  },
  data() {
    return {
      currentMediaType: '',
      currentMediaUrl: '',
      dialogVisible: false
    }
  },
  methods: {
    genderFilter,
    showMediaDetail(meidiaUrl, mediaType) {
      const imageArr = []
      for (let i = 0; i < this.dataArr.length; i++) {
        const item = this.dataArr[i]
        if (item.type === 'image' && item.url) {
          imageArr.push(item.url)
        }
      }
      const params = {
        mediaType: mediaType,
        meidiaUrl: meidiaUrl,
        list: imageArr
      }
      this.$emit('showDetail', params)
    },
    closeVideoDialog() {
      if (this.currentMediaType === 'video') {
        const myVideo = document.getElementById('myVideo')
        myVideo.pause()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.CardContent {
  display: inline-block;
  min-width: 320px;
  max-width: 740px;
  text-align: left;
  .VideoContent {
    position: relative;
    max-width: 350px;
    img {
      max-width: 290px;
      max-height: 260px;
    }
    i.el-icon-video-play {
      position: absolute;
      top: 27%;
      left: 8.5%;
      font-size: 39px;
      color: #fff;
      z-index: 1000;
      &:hover {
        cursor: pointer;
      }
    }
  }
  .data {
    .image-and-video {
      display: inline-block;
      margin-left: 10px;
      width: 75px;
      height: 75px;
      border-radius: 6px;
      &:first-child {
        margin-left: 0;
      }
      .el-icon-video-play {
        top: 21%;
        left: 23%;
      }
    }
    img,
    video {
      position: relative;
      width: 75px;
      height: 75px;
      cursor: pointer;
    }
  }
  .el-dialog__body img {
    cursor: pointer;
    width: 100px;
  }
  .video-container {
    position: relative;
    .video-mask {
      position: absolute;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 75px;
      height: 75px;
      background-color: transparent;
      cursor: pointer;
      span {
        font-size: 20px;
        color: #fff;
        &:hover {
          color: #2d8cf0;
        }
      }
    }
  }
}
</style>
