<template>
  <el-table
    :span-method="objectSpanMethod"
    :data="tableData"
    :header-cell-style="{ background: '#eef1f6', color: '#606266' }"
  >
    <el-table-column align="center" header-align="center" label="任务月龄">
      <template slot-scope="scope">
        {{ scope.row.year }}岁{{ scope.row.month }}月{{ scope.row.day }}天
      </template>
    </el-table-column>
    <el-table-column
      align="center"
      prop="title"
      header-align="center"
      label="标题"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="executeTime"
      width="180px"
      label="任务执行日期"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="vaccine_name"
      label="疫苗名称"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="vaccine_class"
      :formatter="vaccineClass"
      label="疫苗种类"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="vaccine_type"
      :formatter="vaccineType"
      label="疫苗类型"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="vaccine_num"
      label="接种剂次"
    />
    <el-table-column
      align="center"
      header-align="center"
      prop="vaccine_all"
      label="总剂量"
    />
    <el-table-column align="center" header-align="center" label="接种情况">
      <template slot-scope="scope">
        <div style="font-size: 12px">
          {{ scope.row.isVaccine | vaccineStatus }}
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { vaccineType, vaccineClass } from '@/utils/healthManagement'
export default {
  name: 'VaccineTable',
  props: {
    tableData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      spanArr: []
    }
  },
  watch: {
    tableData() {
      this.rowspan()
    }
  },
  created() {
    this.rowspan()
  },
  methods: {
    // 合并单元格
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1 || columnIndex === 2) {
        const _row = this.spanArr[rowIndex]
        const _col = _row > 0 ? 1 : 0
        return {
          rowspan: _row,
          colspan: _col
        }
      }
    },
    rowspan() {
      this.spanArr = []
      const taskList = this.tableData || []
      taskList.forEach((item, index) => {
        if (index === 0) {
          this.spanArr.push(1)
          this.position = 0
        } else {
          const age =
            +taskList[index].year + taskList[index].month + taskList[index].day
          const lastAge =
            +taskList[index - 1].year +
            taskList[index - 1].month +
            taskList[index - 1].day
          if (age === lastAge) {
            this.spanArr[this.position] += 1
            this.spanArr.push(0)
          } else {
            this.spanArr.push(1)
            this.position = index
          }
        }
      })
    },
    // 疫苗类型
    vaccineType,
    // 疫苗种类
    vaccineClass
  }
}
</script>
