<template>
  <div class="customer-info">
    <div v-if="pageType !== 'chat'">
      <p class="color-black margin-right-10 margin-bottom-10">用户信息</p>
    </div>
    <el-row class="clearfix" type="flex" justify="space-between">
      <el-col :span="pageType === 'chat' ? 6 : 3">
        <el-avatar size="large" :src="user.avatar" :key="user.user_id" />
      </el-col>
      <el-col :span="pageType === 'chat' ? 14 : 15">
        <div
          v-if="!remarkEditable"
          :title="edit && role === 'hm' ? '双击修改备注名' : ''"
          style="font-size: 14px;color: #17233D;"
          @dblclick="editRemark(user)"
        >
          <span>
            {{ user.name || '-' }}
          </span>
        </div>
        <input
          v-else
          v-model="remarkName"
          v-myfocus
          style="width: 100px"
          @blur="onRemarkBlur"
        />
        <el-row>
          <span class="span-key">ID：</span>
          <span class="span-val">{{ user.user_id || '-' }}</span>
        </el-row>
      </el-col>
      <el-col :span="4">
        <my-copy class="float-right" :copyKey="user.name"></my-copy>
        <my-copy class="float-right" :copyKey="user.user_id"></my-copy>
      </el-col>
    </el-row>
    <el-row style="margin:6px 0;" type="flex" justify="space-between">
      <el-col :span="18">
        <span class="span-key">昵称：</span
        >{{ user.nickname || user.nick_name || '-' }}
      </el-col>
      <my-copy
        class="float-right"
        :style="pageType !== 'chat' ? 'margin-right:11px' : ''"
        :copyKey="user.nickname || user.nick_name"
      ></my-copy>
    </el-row>
    <el-row>
      <div>
        <span
          class="add_remark_btn"
          :class="role !== 'hm' ? 'notAllow' : ''"
          @click="showAddTextareaFun"
          >{{ tipContent }}备注（最多50字）</span
        >
      </div>

      <div v-if="haveRemark && !showAddTextarea">
        <p v-if="remark" class="remark_text">{{ remark }}</p>
        <p v-if="user.remark && user.remark.created_at" class="created_at">
          {{ formatTime(user.remark.created_at) }}
        </p>
      </div>
      <div v-if="showAddTextarea" class="clearfix">
        <el-input
          v-model="remark"
          type="textarea"
          rows="3"
          maxlength="50"
          show-word-limit
          aria-placeholder="请填写备注信息"
        />
        <el-button
          type="primary"
          class="float-right margin-top-10"
          size="mini"
          @click="saveRemarkFun"
          >保存</el-button
        >
      </div>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { chatUserRemark } from '@/api/userInfo'
import MyCopy from '@/components/MyCopy'
export default {
  name: 'UserInfo',
  props: {
    user: {
      type: Object,
      default: () => {}
    },
    pageType: {
      type: String,
      default: 'chat'
    }
  },
  data() {
    return {
      // 备注内容
      showAddTextarea: false,
      tipContent: '',
      remark: '', //备注
      haveRemark: '', //是否已有备注
      remarkEditable: false,
      remarkName: '',
      edit: true
    }
  },
  components: {
    MyCopy
  },
  computed: {
    ...mapGetters(['role', 'userInfo', 'activedUserId'])
  },
  watch: {
    role() {
      this.edit = this.role === 'hm'
    },
    user(val) {
      this.remark = val && val.remark ? val.remark.content : ''
      this.haveRemark = val && val.remark ? val.remark.content : ''
      this.tipContent = this.haveRemark ? '修改' : '添加'
      this.hideAddTextareaFun()
      const editStatus = window.localStorage.getItem(
        'editStatus' + this.activedUserId
      )
      const userRemark = window.localStorage.getItem(
        'user' + this.activedUserId + 'remark'
      )
      if (editStatus === true) {
        this.showAddTextareaFun()
        this.remark = userRemark
      }
    },
    showAddTextarea(val) {
      if (val) {
        window.localStorage.setItem('editStatus' + this.activedUserId, val)
        window.localStorage.setItem(
          'user' + this.activedUserId + 'remark',
          this.remark
        )
      } else {
        window.localStorage.removeItem('editStatus' + this.activedUserId)
        window.localStorage.removeItem('user' + this.activedUserId + 'remark')
      }
    }
  },
  created() {},
  methods: {
    async changeChatUserRemarkFun(remarkName) {
      // 修改remark只传remark,修改remark_name只传remark_name
      const params = {
        remark: remarkName ? '' : this.remark,
        remark_name: remarkName ? remarkName : ''
      }
      const { data } = await chatUserRemark(this.user._id, params)
      this.$message({
        type: data.result ? 'success' : 'error',
        message: data.result ? '保存成功' : data.message
      })
      if (data && data.result) {
        if (!remarkName) {
          this.hideAddTextareaFun()
          this.haveRemark = true
          this.tipContent = '修改'
        }
        if (this.$route.path.indexOf('diagnose') !== -1) {
          // 刷新聊天窗口顶部客户信息
          this.$store.dispatch('chat/getActivedUserInfo')
        }
        this.$emit('updateInfo')
      }
    },
    // 修改用户备注信息
    showAddTextareaFun() {
      if (this.role === 'hm') {
        this.showAddTextarea = true
      }
    },
    hideAddTextareaFun() {
      this.showAddTextarea = false
    },
    saveRemarkFun() {
      if (!this.remark) {
        this.$message({ type: 'warning', message: '备注不能为空' })
        return
      }
      this.changeChatUserRemarkFun()
    },
    // 修改备注名
    editRemark(user) {
      if (this.role !== 'hm') {
        return
      }
      if (this.edit) {
        this.remarkEditable = true
        this.remarkName = user.name
      }
    },
    onRemarkBlur() {
      this.remarkEditable = false
      this.changeChatUserRemarkFun(this.remarkName)
    }
  }
}
</script>

<style scoped lang="scss">
.customer-info {
  display: flex;
  justify-content: space-around;
  padding: 0 0 10px;
  line-height: 20px;
  font-family: PingFangSC-Medium;
  flex-direction: column;
  .span-key {
    line-height: 16px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    color: #586378;
    letter-spacing: 0;
  }
  .span-val {
    line-height: 16px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    color: #747d8f;
    letter-spacing: 0;
  }
  .package {
    padding: 0;
    border: none;
  }
  .package:hover {
    color: #000;
    background: none;
  }

  .add_remark_btn {
    line-height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Medium;
    text-align: right;
    color: #0085ff;
    letter-spacing: 0;
    cursor: pointer;
  }
  .remark_text {
    overflow: auto;
    max-height: 420px;
    line-height: 19px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    white-space: pre-line;
    color: #576277;
    word-break: break-all;
    letter-spacing: 0;
  }
  .created_at {
    line-height: 18px;
    font-size: 12px;
    font-family: PingFangSC-Regular;
    color: #999;
    letter-spacing: 0;
  }
}
</style>
