import _ from 'lodash'
import $store from '../../store/index'
import {
  curStageTxt,
  booleanToTxt,
  feedModeTxt,
  fenmianModeTxt
} from '@/filters'
export function datePickerOptions() {
  return {
    shortcuts: [
      {
        text: '最近一周',
        onClick(picker) {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
          picker.$emit('pick', [start, end])
        }
      },
      {
        text: '最近一个月',
        onClick(picker) {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
          picker.$emit('pick', [start, end])
        }
      },
      {
        text: '最近三个月',
        onClick(picker) {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
          picker.$emit('pick', [start, end])
        }
      }
    ],
    // 不能将日期范围选在今天之后
    disabledDate(day) {
      const now = new Date().getTime()
      return day > now
    }
  }
}

export function switchDotStyle(timePot) {
  const eventType = timePot.event
  const busRole = timePot.bus_role
  switch (timePot.type) {
    case 'over_time_send_suggestion':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-fsdz',
        color: '#0085FF',
        border: '1px solid #0085FF'
      }
    case 'round_append':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-round_append',
        color: '#67CA39',
        border: '1px solid #67CA39'
      }
    case 'admin_end':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-admin_end',
        color: '#ED3645',
        border: '1px solid #ED3645'
      }
    case 'text_answer':
      return {
        bgColor: '#FFFFFF',
        iconType: 'iconhushi',
        color: '#FF8181',
        border: '1px solid #FF8181'
      }
    case 'system':
      if (eventType === 'error') {
        return {
          bgColor: '#ED3645',
          iconType: 'icon-setting-fill',
          border: '1px solid #ED3645'
        }
      } else if (eventType === 'warning') {
        return {
          bgColor: '#FFB718',
          iconType: 'icon-setting-fill',
          border: '1px solid #FFB718'
        }
      } else if (eventType === 'end') {
        return {
          bgColor: '#979797',
          iconType: 'icon-setting-fill',
          border: '1px solid #979797'
        }
      } else if (eventType === 'transfer_auto_accept') {
        return {
          bgColor: '#67CA39',
          iconType: 'icon-setting-fill',
          border: '1px solid #67CA39'
        }
      } else {
        return {}
      }
    case 'system-info':
      if (eventType === 'error') {
        return {
          bgColor: '#ED3645',
          iconType: 'icon-setting-fill',
          borderColor: '#FFFFFF'
        }
      } else if (eventType === 'warning') {
        return {
          bgColor: '#FFB718',
          iconType: 'icon-setting-fill',
          borderColor: '#FFFFFF'
        }
      } else if (eventType === 'end') {
        return {
          bgColor: '#979797',
          iconType: 'icon-setting-fill',
          borderColor: '#FFFFFF'
        }
      } else {
        return {}
      }
    case 'user_ask':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-signal-fill',
        color: '#0085FF',
        border: '1px solid #0085FF'
      }
    case 'round_apply_transfer':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-signal-fill',
        color: '#0085FF',
        border: '1px solid #0085FF'
      }
    case 'user_end':
      return {
        bgColor: '#FFFFFF',
        iconType: 'el-icon-user-solid',
        color: '#979797',
        border: '1px solid #979797'
      }
    // 医生 同意转诊（同意、拒绝、电话接待）
    // 电话转诊
    case 'voice_transfer_answer':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-yisheng',
        color: '#67CA39',
        border: '1px solid #67CA39'
      }
    case 'text_transfer_answer':
      if (busRole === 'doctor') {
        return {
          bgColor: '#FFFFFF',
          iconType: 'icon-yisheng',
          color: '#67CA39',
          border: '1px solid #67CA39'
        }
      } else if (busRole === 'dietician') {
        return {
          bgColor: '#fff',
          iconType: 'icon-yingyangshi1',
          color: '#FFB718',
          border: '1px solid #FFB718'
        }
      } else {
        return {}
      }
    case 'transfer_apply':
      return {
        bgColor: '#FFFFFF',
        iconType: 'iconhushi',
        color: '#FF8181',
        border: '1px solid #FF8181'
      }
    case 'transfer_start':
      return {
        bgColor: '#FFFFFF',
        iconType: 'iconhushi',
        color: '#FF8181',
        border: '1px solid #FF8181'
      }
    case 'transfer_accept':
      if (busRole === 'doctor') {
        return {
          bgColor: '#FFFFFF',
          iconType: 'icon-yisheng',
          color: '#67CA39',
          border: '1px solid #67CA39'
        }
      } else if (busRole === 'dietician') {
        return {
          bgColor: '#fff',
          iconType: 'icon-yingyangshi1',
          color: '#FFB718',
          border: '1px solid #FFB718'
        }
      } else {
        return {}
      }
    case 'transfer_reject':
      if (busRole === 'doctor') {
        return {
          bgColor: '#FFFFFF',
          iconType: 'icon-yisheng',
          color: '#67CA39',
          border: '1px solid #67CA39'
        }
      } else if (busRole === 'dietician') {
        return {
          bgColor: '#fff',
          iconType: 'icon-yingyangshi1',
          color: '#FFB718',
          border: '1px solid #FFB718'
        }
      } else {
        return {}
      }
    case 'voice_transfer_end': // 电话转诊结束
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-yisheng',
        color: '#999',
        border: '1px solid #999'
      }
    case 'user_transfer':
      return {
        bgColor: '#FFFFFF',
        iconType: 'icon-signal-fill',
        color: '#0085FF',
        border: '1px solid #0085FF'
      }
    case 'iconicon-ysdaiban':
      return {
        bgColor: '#FFFFFF',
        iconType: 'iconicon-ysdaiban',
        color: '#0085FF',
        border: '1px solid #0085FF'
      }
    default:
      return {}
  }
}
// 判断套餐是否包含该服务
export function ifHasServer(serveName) {
  if (!$store.state.chat.serviceItem) {
    return
  }
  const serviceItem = $store.state.chat.serviceItem
  // console.log('套餐包含的服务=', serviceItem)
  if (serveName === 'transfer' && serviceItem.length) {
    let index = false
    for (let i = 0; i < serviceItem.length; i++) {
      if (serviceItem[i].indexOf(serveName) !== -1) {
        index = true
        return index
      }
    }
    return index
  } else {
    let index = _.indexOf(serviceItem, serveName)
    index = index !== -1
    return index
  }
}
// 获取产科信息
export function getChankeInfo(curStage, basicInfo, isHistory, result, time) {
  let format = 'y-m-d'
  let data = [
    {
      show: true,
      label: '当前阶段：',
      value: curStageTxt(curStage)
    },
    {
      show: true,
      label: '月经史（月经是否规律）：',
      value: booleanToTxt(basicInfo.is_schedule)
    },
    {
      show: !basicInfo.is_schedule && basicInfo.measure_desc,
      label: '',
      html: '<span class="color-999">' + basicInfo.measure_desc + '</span>',
      value: ''
    },
    {
      show: basicInfo.is_schedule,
      label: '月经周期：',
      value:
        basicInfo && basicInfo.period_start
          ? basicInfo.period_start + '天 至 ' + basicInfo.period_end + '天'
          : '-'
    },
    {
      show: basicInfo.is_schedule,
      label: '每次持续：',
      value:
        basicInfo && basicInfo.duration_start
          ? basicInfo.duration_start + '天 至 ' + basicInfo.duration_end + '天'
          : '-'
    },
    {
      show: basicInfo.is_schedule,
      label: '月经量：',
      value:
        basicInfo && basicInfo.measure
          ? this.measureToTxt(basicInfo.measure)
          : '-'
    },
    {
      show: curStage === 'byz',
      label: '备孕开始日期：',
      value: time
        ? this.formatTime(basicInfo.started_at, format)
        : basicInfo.started_at
    },
    {
      show: curStage === 'byz',
      label: '备孕时长：',
      value: basicInfo.bysc
    },
    {
      show: curStage === 'byz',
      label: '是否做过孕前检查：',
      value: booleanToTxt(basicInfo.is_checked)
    },
    {
      show: curStage === 'byz' && basicInfo.is_checked,
      label: '检查是否有异常：',
      value: booleanToTxt(basicInfo.has_unusual_status)
    },
    {
      show:
        curStage === 'byz' &&
        basicInfo.is_checked &&
        basicInfo.has_unusual_status,
      label: '',
      html: '<span class="color-999">' + basicInfo.unusual_desc + '</span>',
      value: ''
    },
    {
      show: curStage === 'hyz',
      label: '末次月经日期：',
      value: time
        ? this.formatTime(basicInfo.last_yuejing_at, format)
        : basicInfo.last_yuejing_at
    },
    {
      show: curStage === 'hyz',
      label: '预产期：',
      value: time
        ? this.formatTime(basicInfo.yuchan_at, format)
        : basicInfo.yuchan_at
    },
    {
      show: curStage === 'hyz',
      label: '孕周：',
      value: basicInfo.gestational_weeks
        ? basicInfo.gestational_weeks + '周' + basicInfo.gestational_days + '天'
        : ''
    },
    {
      show: curStage === 'hyz',
      label: '是否孕育过有缺陷的宝宝：',
      value: booleanToTxt(basicInfo.has_unusual_baby)
    },
    {
      show: curStage === 'hyz' && basicInfo.has_unusual_baby,
      label: '',
      html:
        '<span class="color-999">' + basicInfo.unusual_baby_desc + '</span>',
      value: ''
    },
    {
      show: curStage === 'hyz',
      label: '既往妊娠综合症：',
      value: booleanToTxt(basicInfo.has_renshen_history, 'has')
    },
    {
      show: curStage === 'hyz' && basicInfo.has_renshen_history,
      label: '',
      html: '<span class="color-999">' + basicInfo.renshen_desc + '</span>',
      value: ''
    },
    {
      show: curStage === 'crq',
      label: '分娩日期：',
      value: time
        ? this.formatTime(basicInfo.fenmian_at, format)
        : basicInfo.fenmian_at
    },
    {
      show: curStage === 'crq',
      label: '分娩方式：',
      value: fenmianModeTxt(basicInfo.fenmian_mode)
    },
    {
      show: curStage === 'crq' && basicInfo.fenmian_mode === 'ydfm',
      label: '是否侧切：',
      value: booleanToTxt(basicInfo.is_ceqie)
    },
    {
      show: curStage === 'crq',
      label: '本次产后喂养方式：',
      value: feedModeTxt(basicInfo.feed_mode)
    },
    {
      show: result ? true : false,
      label: !isHistory ? '症状描述：' : '',
      value: '',
      html: result ? result.msg_content.content : ''
    }
  ]
  return data
}
